/* swiper CUSTOM */

.swiper {
    width: 100%;
    height: 100%;
}

/* swiper-buttons */
.swiper-btns {
    display: flex;
    align-items: center;
    gap: 6px;
}
.swiper-button-next, .swiper-button-prev,
.swiper-button-next--white, .swiper-button-prev--white {
    position: static;
    margin: 0;
    min-width: 40px;
    width: 40px;
    height: 40px;
    border-radius: 25px;
    background: var(--korichnevyy);
    transition: all 0.3s ease;
}
.swiper-button-next:after, .swiper-button-prev:after {
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    transition: all 0.3s ease;
}
.swiper-button-next:hover,.swiper-button-prev:hover {
    background: var(--svetlo-korichnevyy);
}
.swiper-button-next:hover:after, .swiper-button-prev:hover:after {
    color: var(--korichnevyy);
}


