.swiper-pagination-fraction {
    position: static;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);

    .swiper-pagination-current {
        font-size: 26px;
        color: var(--fon-plashek-seryy);
    }
}

.swiper-scrollbar {
    position: static !important;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 10px;
    &-drag {
        background: var(--korichnevyy);
        border-radius: 10px;
    }
}
