// site-map-1
.site-map .btn-circle {
    background: var(--korichnevyy);
}

.site-map {
    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__accordion {
        display: contents;
    }

    &__item {
        border-radius: 20px;
        padding: 30px;
        background: var(--fon-plashek-bezhevyy);

        &_top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 15px;
            padding-bottom: 15px;
            border-bottom: 1px solid #dbd8d7;
            margin-bottom: 15px;
 
        }
 
        &_title {
            font-family: var(--second-family);
            font-size: 18px;
            line-height: 111%;
            color: var(--tekst-temnyy);
        }


        &_list {
            &.accordion__content {
                display: contents;
            }

            & ul {
                display: flex;
                flex-direction: column;
                gap: 12px;

                & li a {
                    display: flex;
                    gap: 10px;
                    font-family: var(--font-family);
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 140%;
                    color: var(--tekst-svetlyy);
                    transition: all 0.3s ease;

                    &:hover {
                        color: var(--korichnevyy);
                    }

                    &::before {
                        content: "";
                        display: block;
                        border-radius: 10px;
                        min-width: 18px;
                        width: 18px;
                        height: 3px;
                        background: var(--knopka);
                        margin-top: 9px;
                    }
                }
            }
        }
    }

    &__items {
        padding: 30px;

        & ul {
            display: flex;
            flex-direction: column;
            gap: 30px;

            & a {
                font-family: var(--second-family);
                font-size: 18px;
                line-height: 111%;
                color: var(--tekst-temnyy);
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding-bottom: 30px;
                border-bottom: 1px solid #dbd8d7;
                transition: all 0.3s ease;

                &:hover {
                    color: var(--korichnevyy);
                }
            }
        }
    }
}

// media site-map-1
@media (max-width: 1050px) {
    .site-map {
        &__grid {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}

@media (max-width: 700px) {
    .site-map {
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 12px;
        }

        &__accordion {
            display: block;

            &.accordion--active {
                padding-bottom: 15px;
                border-bottom: 1px solid #c7d6dc;
                margin-bottom: 15px;

                & .site-map__brn-more::after {
                    transform: rotate(315deg);
                    margin: 0;
                }
            }
        }

        &__item {
            border-radius: 10px;
            padding: 20px;

            &_top {
                padding-bottom: 0;
                border-bottom: none;
                margin-bottom: 0;
            }

            &_list {
                &.accordion__content {
                    display: block;
                }


            }

            &_title {
                font-size: 18px;
            }

            & .site-map__brn-more::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 2px;
            }

            &_list {
                & ul {
                    & li a {
                        font-family: var(--font-family);
                        font-weight: 400;
                        font-size: 14px;
                        line-height: 140%;
                        color: var(--tekst-svetlyy);
                    }
                }
            }
        }

        &__items {
            padding: 0 10px;
            margin-top: 20px;
            & ul {
                a {
                    font-size: 16px;
                    padding-bottom: 0;
                    border-bottom: none;
                }
            }
        }
    }
}