
/*  glob */

html.lock {
    overflow: hidden;
}
html.burger-lock {
    overflow: hidden;
}
html.burger-lock::before {
    opacity: 1;
}
html::before {
    display: block;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.3215686275);
    position: fixed;
    inset: 0;
    opacity: 0;
    z-index: 10;
    pointer-events: none;
    -webkit-transition: opacity 0.3s linear;
    -o-transition: opacity 0.3s linear;
    transition: opacity 0.3s linear;
}
.no-scroll {
    overflow: hidden;
}
body.lock {
    overflow: hidden;
}
body.burger-lock {
    overflow: hidden;
}

body {
    margin: 0;
    //overflow-x: hidden;
    overflow-y: auto;
    padding: 0;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
    font-family: var(--font-family);
    background-color: #fff;
}
.container {
	margin: 0 auto;
	padding: 0 var(--container-padding);
	max-width: var(--container-width);
	width: 100%;
}

.section-offset {
    margin-bottom: 140px;
}

.section__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 50px;
}

.section__title {
    margin-bottom: 50px;
}

.bg {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    & img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}



/*  buttons */
.primary-btn, .secondary-btn, .tertiary-btn {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    text-align: center;
    border-radius: 60px;
    padding: 16px 20px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    transition: .2s;
    &::after {
        content: "";
        background:  white;
        position: absolute;
        z-index: -1;
        padding: 0.85em 0.75em;
        display: block;
        left: -2%;
        right: -2%;
        top: 0;
        bottom: -2px;
        transform: skewX(-45deg) scale(0, 1);
        transition: all 0.3s ease;
    }
    & span {
        position: relative;
        z-index: 2;
    }
    &:active {
        transform: scale(0.95);
    }
    &:hover {
        background-position: 0;
    }
}
@media (hover: hover) {
    .primary-btn, .secondary-btn, .tertiary-btn {
        &:hover::after {
            transform: skewX(-45deg) scale(1, 1);
            transition: all 0.3s ease-out;
        }
    }
}

.primary-btn {
    color: var(--belyy);
    background: var(--knopka);
    position: relative;
    overflow: hidden;
    &::after {
        background:  rgba(8, 11, 15, 0.2);
    }
}

.secondary-btn {
    color: var(--knopka);
    background: var(--belyy);
    z-index: 1;
    &::after {
        background: var(--svetlo-korichnevyy);
    }
}

.tertiary-btn {
    color: var(--knopka);
    border: 1px solid var(--knopka);
    &::after {
        background: var(--knopka);
    }
    &:hover {
        color: var(--belyy);
    }
}


.btn-arrow {
    display: block;
    font-weight: 600;
    font-size: 14px;
    line-height: 140%;
    text-align: left;
    color: var(--knopka);
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid var(--knopka);
        border-right: 1px solid var(--knopka);
        transform: rotate(45deg);
        margin-left: 8px;
        transition: all 0.3s ease;
    }
    &:hover {
        opacity: 0.5;
    }
}
@media (hover: hover) {
    .btn-arrow {
        &:hover {
            opacity: 0.5;
            &::after {
                transform: rotate(45deg) translate(5px, -5px);
            }
        }
    }
}

.btn-circle {
    display: block;
    border-radius: 25px;
    width: 30px;
    min-width: 30px;
    height: 30px;
    background: var(--knopka);
    color: var(--belyy);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.3s ease;

    &::after {
        content: "";
        display: inline-block;
        width: 8px;
        height: 8px;
        border-top: 1px solid var(--belyy);
        border-right: 1px solid var(--belyy);
        transform: rotate(45deg);
        margin-right: 4px;
        transition: all 0.3s ease;
    }
    &:hover {
        opacity: 0.5;
    }
}

.btn-more.active {
    &::after {
        transform: rotate(315deg);
    }
}



/*  titles  */

.title-h1, .title-h2, .title-h3, .title-h4, .title-h5, .title-h6 {
    font-family: var(--second-family);
    font-weight: 400;
    line-height: 140%;
    color: var(--tekst-temnyy);
}
.title-h1 {
    font-size: 44px;
    line-height: 120%;
    color: #fff;
}
.title-h2 {
    font-size: 38px;
}
.title-h3 {
    font-size: 34px;
}
.title-h4 {
    font-size: 30px;
}
.title-h5 {
    font-size: 26px;
}


.phone {
    font-weight: 700;
    font-size: 22px;
    line-height: 140%;
    color: var(--color-zelenyy);
    display: flex;
    gap: 10px;
    align-items: center;
    transition: all 0.3s ease;

    &:hover {
        opacity: 0.5;
    }
}

.phone {
    font-weight: 700;
    font-size: 22px;
    line-height: 140%;
    color: var(--color-zelenyy);
    display: flex;
    gap: 10px;
    align-items: center;
    transition: all 0.3s ease;

    &:hover {
        opacity: 0.5;
    }
}

.phone-btn,  .phone-btn-white {
    border-radius: 60px;
    padding: 10px 20px;
    font-weight: 700;
    font-size: 22px;
    line-height: 140%;
    display: flex;
    gap: 10px;
    align-items: center;
    width: max-content;
    min-width: max-content;
    transition: all 0.3s ease;
    & svg path {
        transition: all 0.3s ease;
    }
}
.phone-btn-white {
    background: var(--belyy);
    color: var(--tekst-temnyy);
    &:hover {
        background: var(--tekst-temnyy);
        color: var(--belyy);
        & svg path {
            fill: var(--belyy);
            stroke: var(--belyy);
        }
    }
}
.phone-btn {
    background: var(--svetlo-korichnevyy-2);
    color: var(--knopka);
    &:hover {
        background: var(--svetlo-korichnevyy);
    }
}


/*  form  */

.input, .textarea {
    background: var(--belyy);
    border-radius: 60px;
    padding: 16px 24px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-temnyy);
    border: 1px solid transparent;
    transition: all 0.3s ease;
}
.input::placeholder, .textarea::placeholder  {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
    color: var(--tekst-svetlyy);
}
.input:hover, .textarea:hover, .input:focus, .textarea:focus, .input:active, .textarea:active {
    border: 1px solid var(--liniya);
    outline: none;
}


.input-white, .textarea-white {
    background: rgba(206, 206, 206, 0.1);
    color: #fff;
}
.input-white::placeholder, .textarea-white::placeholder  {
    color: rgba(255, 255, 255, 0.8);
}
.input-white:hover, .textarea-white:hover, .input-white:focus, .textarea-white:focus, .input-white:active, .textarea-white:active {
    border: 1px solid #fff;
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
	-webkit-appearance:none;
}


.input-wrapper {
    position: relative;
    border-radius: 5px;
    overflow: hidden;
    & .input {
        width: 100%;
    }
}
.input-clear {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translate(0, -50%);
    z-index: 5;
    cursor: pointer;
}

.textarea {
    width: 100%;
    min-height: 142px;
    border-radius: 15px;
    resize: none;
}

.politic {
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
    opacity: 0.8;
    & a {
        color: inherit;
        font-weight: 500;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        transition: all 0.3s ease;
        &:hover {
            opacity: 1;
        }
    }
}
.politic-white {
    color: #fff;
    opacity: 0.6;
}

/*  search  */
.form-search {
    position: relative;
    width: 100%;
    & input {
        border: 1px solid var(--liniya);
        opacity: 0.7;
        width: 100%;
        &:focus {
            opacity: 1;
        }
    }
    & img {
        position: absolute;
        top: 50%;
        right: 20px;
        transform: translate(0, -50%);
    }
}


/* select */
select, .select {
    width: 100%;
    max-width: 100%;
    cursor: pointer;
    outline: none;
    border-radius: 60px;
    padding: 28px 30px 10px 24px;
    border: 1px solid var(--liniya);
    font-weight: 500;
    color: var(--tekst-temnyy);
    opacity: 0.7;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    background: url(./../img/icons/arrow-bottom.svg) no-repeat right;
    background-position-x: calc(100% - 20px);
    background-position-y: calc(100% - 18px);
    transition: all 0.3s ease;

    &:hover {
        opacity: 1;
    }
    &:focus {
        opacity: 1;
        outline: 1px solid var(--liniya);
    }
}


.sort_block {
    position: relative;
    & label {
        display: block;
        font-size: 12px;
        line-height: 140%;
        color: var(--tekst-svetlyy);
        opacity: 0.7;
        position: absolute;
        top: 10px;
        left: 24px;
        z-index: 2;
    }
}

/* range */
input[type="range"] {
    -webkit-appearance: none; 
    appearance: none;
    width: 100%; 
    height: 4px; 
    background: var(--color-svetlo-zelenyy-1);
    border-radius: 20px;
    outline: none; 
    -webkit-transition: .2s;
    transition: opacity .2s;
    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        appearance: none;
        width: 17px;
        height: 17px;
        border-radius: 100%; 
        background: var(--color-zelenyy);
        cursor: pointer;
    }
    &::-moz-range-thumb {
        width: 17px;
        height: 17px;
        border-radius: 100%; 
        background: var(--color-zelenyy);
        cursor: pointer;
    }
    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
    &:focus::-moz-range-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
    &:focus::-webkit-slider-thumb {
        outline: 2px solid var(--color-zelenyy);
        outline-offset: 0.125rem;
    }
}




/* accordion */
.accordion {
    -webkit-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
    cursor: pointer;
    width: 100%;
    position: relative;

    &--active {
        .accordion__btn-more {
            transform: rotate(180deg);
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        -webkit-transition: all 0.4s ease;
        -o-transition: all 0.4s ease;
        transition: all 0.4s ease;
    }
}

/* tabs */
.tab__btns {
    display: flex;
    flex-wrap: wrap;
    gap: 20px 0;
}
.tab__btn {
    transition: all 0.3s ease;
    color: var(--tekst-svetlyy);
    border-bottom: 1px solid var(--liniya);
    padding: 0px 40px 20px;
    &.active {
        color: var(--knopka);
        border-bottom: 1px solid var(--knopka);
        -webkit-text-stroke: 0.5px var(--knopka);
        text-stroke: 0.5px var(--knopka);
    }
}
.tabcontent {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}
.tab-content {
    display: none;
    &.active {
        display: block;
        width: 100%;
        min-height: 100%;
    }
}

/* rating */
.full-stars {
    text-align: center;
}
.full-stars .rating-group {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
}
.full-stars input {
    position: absolute;
    left: -9999px;
}
.full-stars label {
    margin: 0;
    cursor: pointer;
}
.full-stars label svg {
    margin: 0 2px;
    height: 18px;
    width: 20px;
    fill: var(--tekst-temnyy);
    -webkit-transition: fill 0.3s;
    -o-transition: fill 0.3s;
    transition: fill 0.3s;
}
.full-stars input:checked ~ label svg {
    fill: transparent;
}
.full-stars .rating-group:hover label svg {
    fill: var(--tekst-temnyy);
}
.full-stars .rating-group input:hover ~ label svg {
    fill: transparent;
}



/*  media global  */
@media (max-width: 800px) {
    .section-offset {
        margin-bottom: 60px;
    }
    .section {
        &__flex {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        &__top {
            display: contents;
            margin-bottom: 20px;
        }
        &__btn {
            display: block;
            order: 3; 
            width: 100%; 
        }
        &__inner {
            order: 2;
        }
    }
}
@media (max-width: 688px) {
    body {
        font-size: 14px;
    }
    .section__title {
        margin-bottom: 20px;
    }
    .title-h1 {
        font-size: 28px;
    }
    .title-h2 {
        font-size: 24px;
    }
    .title-h3 {
        font-size: 22px;
    }
    .title-h4 {
        font-size: 20px;
    }
    .title-h5 {
        font-size: 18px;
    }
    .title-h6 {
        font-size: 16px;
        line-height: 125%;
    }
    .btn-circle {
        width: 25px;
        min-width: 25px;
        height: 25px;
        &::after {
            width: 6px;
            height: 6px;
            margin-right: 1px;
        }
    }
    .phone {
        font-size: 18px;
    }
    .phone-btn,  .phone-btn-white {
        padding: 10px 16px;
        font-size: 18px;
    }

    .sort {
        &__text {
            margin-bottom: 14px;
        }
        &__item {
            margin-bottom: 14px;
            & input[type="checkbox"] {
                width: 22px;
                height: 22px;
                &::before {
                    top: 3px;
                    left: 7px;
                    width: 6px;
                    height: 10px;
                    border-width: 0 2px 2px 0;
                }
            }
            & label {
                font-size: 16px;
            }
        }
    }
}


