// form-2
.form-2 {
    padding: 100px 0;
    position: relative;
    overflow: hidden;

    &__bg {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__row {
        display: flex;
        justify-content: end;
        width: 100%;
        position: relative;
    }

    &__wrapper {
        width: 55%;
        position: relative;
        z-index: 2;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 40px;
        line-height: 120%;
        color: var(--fon-plashek-seryy);
        margin-bottom: 40px;
    }

    &__subtitle {
        color: var(--belyy);
        display: flex;
        gap: 25px;
        margin-bottom: 40px;
    }

    &__content {
        border-radius: 20px;
        padding: 40px;
        backdrop-filter: blur(10px);
        background: rgba(65, 66, 86, 0.6);
        color: var(--fon-plashek-seryy);
    }

    &__text {
        font-size: 16px;
        margin-bottom: 30px;
    }

    &__inner {
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
    }

    &__input, &__btn {
        flex: 1 0 25%;
    }

    &__img {
        position: absolute;
        bottom: -100px;
        left: 0;
        width: 50%;
        max-width: 630px;
    }
}

.form-white {
    background: var(--fon-plashek-bezhevyy);
    color: var(--tekst-svetlyy);
    & .form-2__title {
        color: var(--tekst-temnyy);
    }
    & .form-2__subtitle {
        color: var(--tekst-svetlyy);
    }
    & .form-2__content {
        background: var(--svetlo-korichnevyy-2);
        color: var(--tekst-svetlyy);
    }
}

// media form-2
@media(max-width: 1100px) {
    .form-2 {
        &__row {
            flex-direction: column;
        }
        &__wrapper {
            width: 100%;
            display: contents;
        }
        &__img {
            display: none;
        }
    }
}
@media(max-width: 800px) {
    .form-2 {
        &__input, &__btn {
            flex: 1 0 100%;
        }
    }
}
@media(max-width: 688px) {
    .form-2 {
        padding: 60px 0;
        &__title {
            font-size: 24px;
            margin-bottom: 20px;
        }
        &__subtitle {
            flex-direction: column;
            gap: 20px;
            margin-bottom: 20px;
        }
        &__content {
            border-radius: 20px;
            padding: 20px;
        }
        &__text {
            margin-bottom: 20px;
        }
        &__img {
            display: none;
        }
    }
}