/*  glob */
html.lock {
  overflow: hidden;
}

html.burger-lock {
  overflow: hidden;
}

html.burger-lock::before {
  opacity: 1;
}

html::before {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3215686275);
  position: fixed;
  inset: 0;
  opacity: 0;
  z-index: 10;
  pointer-events: none;
  -webkit-transition: opacity 0.3s linear;
  -o-transition: opacity 0.3s linear;
  transition: opacity 0.3s linear;
}

.no-scroll {
  overflow: hidden;
}

body.lock {
  overflow: hidden;
}

body.burger-lock {
  overflow: hidden;
}

body {
  margin: 0;
  overflow-y: auto;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: var(--tekst-svetlyy);
  font-family: var(--font-family);
  background-color: #fff;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-padding);
  max-width: var(--container-width);
  width: 100%;
}

.section-offset {
  margin-bottom: 140px;
}

.section__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  margin-bottom: 50px;
}

.section__title {
  margin-bottom: 50px;
}

.bg {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/*  buttons */
.primary-btn, .secondary-btn, .tertiary-btn {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
  text-align: center;
  border-radius: 60px;
  padding: 16px 20px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  transition: 0.2s;
}
.primary-btn::after, .secondary-btn::after, .tertiary-btn::after {
  content: "";
  background: white;
  position: absolute;
  z-index: -1;
  padding: 0.85em 0.75em;
  display: block;
  left: -2%;
  right: -2%;
  top: 0;
  bottom: -2px;
  transform: skewX(-45deg) scale(0, 1);
  transition: all 0.3s ease;
}
.primary-btn span, .secondary-btn span, .tertiary-btn span {
  position: relative;
  z-index: 2;
}
.primary-btn:active, .secondary-btn:active, .tertiary-btn:active {
  transform: scale(0.95);
}
.primary-btn:hover, .secondary-btn:hover, .tertiary-btn:hover {
  background-position: 0;
}

@media (hover: hover) {
  .primary-btn:hover::after, .secondary-btn:hover::after, .tertiary-btn:hover::after {
    transform: skewX(-45deg) scale(1, 1);
    transition: all 0.3s ease-out;
  }
}
.primary-btn {
  color: var(--belyy);
  background: var(--knopka);
  position: relative;
  overflow: hidden;
}
.primary-btn::after {
  background: rgba(8, 11, 15, 0.2);
}

.secondary-btn {
  color: var(--knopka);
  background: var(--belyy);
  z-index: 1;
}
.secondary-btn::after {
  background: var(--svetlo-korichnevyy);
}

.tertiary-btn {
  color: var(--knopka);
  border: 1px solid var(--knopka);
}
.tertiary-btn::after {
  background: var(--knopka);
}
.tertiary-btn:hover {
  color: var(--belyy);
}

.btn-arrow {
  display: block;
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
  text-align: left;
  color: var(--knopka);
  transition: all 0.3s ease;
}
.btn-arrow::after {
  content: "";
  display: inline-block;
  width: 8px;
  height: 8px;
  border-top: 1px solid var(--knopka);
  border-right: 1px solid var(--knopka);
  transform: rotate(45deg);
  margin-left: 8px;
  transition: all 0.3s ease;
}
.btn-arrow:hover {
  opacity: 0.5;
}

@media (hover: hover) {
  .btn-arrow:hover {
    opacity: 0.5;
  }
  .btn-arrow:hover::after {
    transform: rotate(45deg) translate(5px, -5px);
  }
}
.btn-circle {
  display: block;
  border-radius: 25px;
  width: 30px;
  min-width: 30px;
  height: 30px;
  background: var(--knopka);
  color: var(--belyy);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}
.btn-circle::after {
  content: "";
  display: inline-block;
  width: 8px;
  height: 8px;
  border-top: 1px solid var(--belyy);
  border-right: 1px solid var(--belyy);
  transform: rotate(45deg);
  margin-right: 4px;
  transition: all 0.3s ease;
}
.btn-circle:hover {
  opacity: 0.5;
}

.btn-more.active::after {
  transform: rotate(315deg);
}

