  font-weight: 400;
  line-height: 140%;
  color: var(--tekst-temnyy);
}

.title-h1 {
  font-size: 44px;
  line-height: 120%;
  color: #fff;
}

.title-h2 {
  font-size: 38px;
}

.title-h3 {
  font-size: 34px;
}

.title-h4 {
  font-size: 30px;
}

.title-h5 {
  font-size: 26px;
}

.phone {
  font-weight: 700;
  font-size: 22px;
  line-height: 140%;
  color: var(--color-zelenyy);
  display: flex;
  gap: 10px;
  align-items: center;
  transition: all 0.3s ease;
}
.phone:hover {
  opacity: 0.5;
}

.phone {
  font-weight: 700;
  font-size: 22px;
  line-height: 140%;
  color: var(--color-zelenyy);
  display: flex;
  gap: 10px;
  align-items: center;
  transition: all 0.3s ease;
}
.phone:hover {
  opacity: 0.5;
}

.phone-btn, .phone-btn-white {
  border-radius: 60px;
  padding: 10px 20px;
  font-weight: 700;
  font-size: 22px;
  line-height: 140%;
  display: flex;
  gap: 10px;
  align-items: center;
  width: max-content;
  min-width: max-content;
  transition: all 0.3s ease;
}
.phone-btn svg path, .phone-btn-white svg path {
  transition: all 0.3s ease;
}

.phone-btn-white {
  background: var(--belyy);
  color: var(--tekst-temnyy);
}
.phone-btn-white:hover {
  background: var(--tekst-temnyy);
  color: var(--belyy);
}
.phone-btn-white:hover svg path {
  fill: var(--belyy);
  stroke: var(--belyy);
}

.phone-btn {
  background: var(--svetlo-korichnevyy-2);
  color: var(--knopka);
}
.phone-btn:hover {
  background: var(--svetlo-korichnevyy);
}

/*  form  */
.input, .textarea {
  background: var(--belyy);
  border-radius: 60px;
  padding: 16px 24px;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: var(--tekst-temnyy);
  border: 1px solid transparent;
  transition: all 0.3s ease;
}

.input::placeholder, .textarea::placeholder {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 14px;
  line-height: 100%;
  color: var(--tekst-svetlyy);
}

.input:hover, .textarea:hover, .input:focus, .textarea:focus, .input:active, .textarea:active {
  border: 1px solid var(--liniya);
  outline: none;
}

.input-white, .textarea-white {
  background: rgba(206, 206, 206, 0.1);
  color: #fff;
}

.input-white::placeholder, .textarea-white::placeholder {
  color: rgba(255, 255, 255, 0.8);
}

.input-white:hover, .textarea-white:hover, .input-white:focus, .textarea-white:focus, .input-white:active, .textarea-white:active {
  border: 1px solid #fff;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  -webkit-appearance: none;
}

.input-wrapper {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
}
.input-wrapper .input {
  width: 100%;
}

.input-clear {
  position: absolute;
  top: 50%;
  right: 16px;
  transform: translate(0, -50%);
  z-index: 5;
  cursor: pointer;
}

.textarea {
  width: 100%;
  min-height: 142px;
  border-radius: 15px;
  resize: none;
}

.politic {
  font-size: 14px;
  line-height: 140%;
  color: var(--tekst-svetlyy);
  opacity: 0.8;
}
.politic a {
  color: inherit;
  font-weight: 500;
  text-decoration: underline;
  text-decoration-skip-ink: none;
  transition: all 0.3s ease;
}
.politic a:hover {
  opacity: 1;
}

.politic-white {
  color: #fff;
  opacity: 0.6;
}

/*  search  */
.form-search {
  position: relative;
  width: 100%;
}
.form-search input {
  border: 1px solid var(--liniya);
  opacity: 0.7;
  width: 100%;
}
.form-search input:focus {
  opacity: 1;
}
.form-search img {
  position: absolute;
  top: 50%;
  right: 20px;
  transform: translate(0, -50%);
}

/* select */
select, .select {
  width: 100%;
  max-width: 100%;
  cursor: pointer;
  outline: none;
  border-radius: 60px;
  padding: 28px 30px 10px 24px;
  border: 1px solid var(--liniya);
  font-weight: 500;
  color: var(--tekst-temnyy);
  opacity: 0.7;
  -webkit-appearance: none;
  -moz-appearance: none;
  -ms-appearance: none;
  appearance: none;
  background: url(./../img/icons/arrow-bottom.svg) no-repeat right;
  background-position-x: calc(100% - 20px);
  background-position-y: calc(100% - 18px);
  transition: all 0.3s ease;
}
select:hover, .select:hover {
  opacity: 1;
}
select:focus, .select:focus {
  opacity: 1;
  outline: 1px solid var(--liniya);
}

.sort_block {
  position: relative;
}
.sort_block label {
  display: block;
  font-size: 12px;
  line-height: 140%;
  color: var(--tekst-svetlyy);
  opacity: 0.7;
  position: absolute;
  top: 10px;
  left: 24px;
  z-index: 2;
}

/* range */
input[type=range] {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 4px;
  background: var(--color-svetlo-zelenyy-1);
  border-radius: 20px;
  outline: none;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 17px;
  height: 17px;
  border-radius: 100%;
  background: var(--color-zelenyy);
  cursor: pointer;
}
input[type=range]::-moz-range-thumb {
  width: 17px;
  height: 17px;
  border-radius: 100%;
  background: var(--color-zelenyy);
  cursor: pointer;
}
input[type=range]:focus::-webkit-slider-thumb {
  outline: 2px solid var(--color-zelenyy);
  outline-offset: 0.125rem;
}
input[type=range]:focus::-moz-range-thumb {
  outline: 2px solid var(--color-zelenyy);
  outline-offset: 0.125rem;
}
input[type=range]:focus::-webkit-slider-thumb {
  outline: 2px solid var(--color-zelenyy);
  outline-offset: 0.125rem;
}

/* accordion */
.accordion {
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
  cursor: pointer;
  width: 100%;
  position: relative;
}
.accordion--active .accordion__btn-more {
  transform: rotate(180deg);
}
.accordion__content {
  max-height: 0;
  overflow: hidden;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

/* tabs */
.tab__btns {
  display: flex;
  flex-wrap: wrap;
  gap: 20px 0;
}

.tab__btn {
  transition: all 0.3s ease;
  color: var(--tekst-svetlyy);
  border-bottom: 1px solid var(--liniya);
  padding: 0px 40px 20px;
}
.tab__btn.active {
  color: var(--knopka);
  border-bottom: 1px solid var(--knopka);
  -webkit-text-stroke: 0.5px var(--knopka);
  text-stroke: 0.5px var(--knopka);
}

.tabcontent {
  display: none;
}
.tabcontent.active {
  display: block;
  width: 100%;
  min-height: 100%;
}

.tab-content {
  display: none;
}
.tab-content.active {
  display: block;
  width: 100%;
  min-height: 100%;
}

/* rating */
.full-stars {
  text-align: center;
}

.full-stars .rating-group {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}

.full-stars input {
  position: absolute;
  left: -9999px;
}

.full-stars label {
  margin: 0;
  cursor: pointer;
}

.full-stars label svg {
  margin: 0 2px;
  height: 18px;
  width: 20px;
  fill: var(--tekst-temnyy);
  -webkit-transition: fill 0.3s;
  -o-transition: fill 0.3s;
  transition: fill 0.3s;
}

.full-stars input:checked ~ label svg {
  fill: transparent;
}

.full-stars .rating-group:hover label svg {
  fill: var(--tekst-temnyy);
}

.full-stars .rating-group input:hover ~ label svg {
