/* breadcrumbs */
.page-top {
  border-radius: 0 0 20px 20px;
  position: relative;
  overflow: hidden;
  margin-bottom: 70px;
}
.page-top .container {
  position: relative;
  z-index: 2;
}
.page-top__bg {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.page-top__bg img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.breadcrumbs {
  padding: 50px 0 40px 0;
  font-family: var(--font-family);
}

.breadcrumbs,
.breadcrumbs li {
  align-items: center;
  display: flex;
  gap: 6px;
  flex-wrap: wrap;
}

.breadcrumbs li, .breadcrumbs li a {
  font-size: 14px;
  color: #fff;
}

.breadcrumbs li a {
  opacity: 0.6;
  transition: all 0.3s ease;
}
.breadcrumbs li a:hover {
  opacity: 1;
}

.breadcrumbs li:after {
  content: ">";
  display: block;
  font-size: 20px;
  color: #fff;
  opacity: 0.6;
  font-size: inherit;
}

.breadcrumbs li:first-child {
  position: relative;
  padding-left: 24px;
}

.breadcrumbs li:first-child::before {
  content: "";
  position: absolute;
  top: 45%;
  left: 0;
  transform: translateY(-50%);
  min-width: 16px;
  width: 16px;
  height: 16px;
  max-height: 16px;
  background: url("./../img/breadcrumbs/home.svg") no-repeat center center/contain;
}

.breadcrumbs li:last-child::after {
  content: none;
}

.page-top__title {
  margin: 20px 0 50px 0;
}

