  width: 100%;
  height: 100%;
  object-position: top;
}

.doc-check__info-4 {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  gap: 10px;
}

.navigation__wrap-4 {
  flex-basis: calc(50% - 10px);
}

.navigation__title-4 {
  margin-bottom: 30px;
}

.navigation__list-4 {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.navigation__link-4 {
  position: relative;
  transition: all 0.3s ease;
  font-weight: 600;
}
.navigation__link-4:hover {
  text-decoration: none;
}

.navigation__item-4 {
  font-size: 16px;
  font-weight: 400;
  line-height: 1.56;
}

.navigation__list-4 .nav-title-h3 .navigation__link,
.navigation__list-4 .nav-title-h4 .navigation__link,
.navigation__list-4 .nav-title-h5 .navigation__link {
  display: flex;
  gap: 10px;
  padding-left: 40px;
  color: var(--tekst-svetlyy);
  font-weight: 400;
  transition: color 0.3s ease;
  position: relative;
}
.navigation__list-4 .nav-title-h3 .navigation__link:after,
.navigation__list-4 .nav-title-h4 .navigation__link:after,
.navigation__list-4 .nav-title-h5 .navigation__link:after {
  content: "";
  position: absolute;
  left: 20px;
  top: 8px;
  width: 12px;
  height: 3px;
  border-radius: 10px;
  background: var(--seryy);
}
.navigation__list-4 .nav-title-h3 .navigation__link:hover,
.navigation__list-4 .nav-title-h4 .navigation__link:hover,
.navigation__list-4 .nav-title-h5 .navigation__link:hover {
  color: var(--korichnevyy);
}

