  gap: 10px;
  padding-left: 20px;
  color: var(--tekst-temnyy);
  font-weight: 700;
  position: relative;
  transition: color 0.3s ease;
}
.navigation__list-4 .nav-title-h2 .navigation__link:after {
  content: "";
  position: absolute;
  left: 0;
  top: 8px;
  width: 15px;
  height: 3px;
  border-radius: 10px;
  background: var(--seryy);
}
.navigation__list-4 .nav-title-h2 .navigation__link:hover {
  color: var(--korichnevyy);
}

.doc-check__post-4 {
  position: relative;
  padding-left: 20px;
}
.doc-check__post-4:after {
  content: "";
  position: absolute;
  top: 3px;
  left: 0;
  width: 14px;
  height: 16px;
  background: url("./../img/icons/doctor.svg") no-repeat center center/cover;
}

.doc-check__stage-4 {
  position: relative;
  padding-left: 20px;
}
