  .popup-search__search-result {
    max-height: 500px;
  }
  .popup-search__list {
    flex-direction: column;
    flex-wrap: nowrap;
    gap: 0;
    max-height: 500px;
    overflow-y: auto;
    padding-right: 10px;
  }
  .popup-search__list::-webkit-scrollbar {
    width: 4px;
    background: var(--liniya);
    padding: 5px;
  }
  .popup-search__list::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: var(--knopka);
  }
  .popup-search__list li a {
    display: block;
    border: none;
    border-bottom: 1px solid var(--liniya);
    border-radius: 0;
    padding: 16px 0;
    font-size: 14px;
    color: var(--knopka);
  }
  .popup-search__list li a:hover {
    color: var(--tekst-temnyy);
  }
  .popup-search__list_result li a {
    padding: 14px;
    margin: 0 4px 0 0;
  }
  .popup-search__form, .popup-search__search, .popup-search__search-result {
    width: 100%;
  }
  .popup-search__close {
    width: 30px;
    height: 30px;
    top: -40px;
    right: -20px;
  }
}