// articles-3
.articles {
    padding: 120px 0;
    border-radius: 0 0 30px 30px;
    background: var(--fon-plashek-bezhevyy);
    overflow: hidden;

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        margin-bottom: 40px;
    }

    &__card {
        display: flex;
        flex-direction: column;
        height: 100%;
        border-radius: 20px;
        background: var(--belyy);
        color: var(--tekst-svetlyy);
        position: relative;

        &_picture {
            display: block;
            border-radius: 20px;
            overflow: hidden;
            position: relative;
            width: 100%;
            height: 240px;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_labels {
            display: flex;
            flex-wrap: wrap;
            gap: 6px;
            position: absolute;
            top: 20px;
            left: 20px;
            right: 20px;
            & p {
                border-radius: 30px;
                padding: 8px 18px;
                backdrop-filter: blur(9.600000381469727px);
                background: rgba(255, 255, 255, 0.4);
                font-weight: 600;
                font-size: 14px;
                line-height: 120%;
                color: #fff;
            }
        }

        &_inner {
            border-radius: 20px;
            padding: 30px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 40px;
        }

        &_title {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 14px;
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }

        &_date {
            font-size: 14px;
            color: var(--tekst-svetlyy);
            border-radius: 30px;
            padding: 8px 14px;
            background: var(--fon-plashek-bezhevyy);
        }
    }
}


// media articles-3
@media (max-width: 800px) {
    .articles {
        padding: 100px 0;
        &__cards {
            margin-bottom: 20px;
        }
        &__swiper-btns{
            justify-content: center;
        }
    }
}
@media (max-width: 688px) {
    .articles {
        padding: 60px 0;
        &__card {
            &_picture {
                height: 200px;
            }
            &_labels {
                flex-wrap: wrap;
                & p {
                    padding: 6px 10px;
                    font-size: 12px;
                }
            }
            &_inner {
                padding: 20px;
                height: calc(100% - 200px);
            }
            &_title {
                font-size: 18px;
            }
            &_date {
                font-size: 12px;
                padding: 8px 12px;
            }
        }
    }
}


