// intro-article-4
.intro-article {
    position: relative;
    padding-bottom: 50px;
    border-radius: 0 0 20px 20px;
    overflow: hidden;

    &__row {
        display: flex;
        gap: 40px;
    }
    &__main {
        display: flex;
        flex-direction: column;
        gap: 40px;
        flex: 1 0 40%;

        & h1, h2 {
            color: var(--belyy);
            margin-bottom: 15px;
        }
        & p {
            color: var(--belyy);
        }
    }

    &__right {
        display: flex;
        gap: 10px;
        flex: 1 0 40%;
    }

    &__marks {
        flex: 1 0 40%;
        display: flex;
        flex-direction: column;
        gap: 10px;
        & p {
            border-radius: 60px;
            padding: 14px 20px;
            backdrop-filter: blur(5px);
            background: rgba(255, 255, 255, 0.1);
            line-height: 140%;
            color: var(--belyy);
            & span {
                font-weight: 600;
            }
        }
    }

    &__form {
        flex: 1 0 45%;
        border-radius: 20px;
        padding: 24px 30px;
        background: var(--tekst-temnyy);;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        min-height: 100%;
        &_title {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 22px;
            line-height: 140%;
            color: #fff;
            margin-bottom: 8px;
        }
        &_subtitle {
            font-size: 14px;
            line-height: 140%;
            color: #fff;
        }
        &_phone {
            font-size: 18px;
            color: #fff;
        }
    }
}

// media intro-article-4
@media (max-width: 1160px) {
    .intro-article {
        &__row {
            flex-direction: column;
            gap: 24px;
        }
    }
}
@media (max-width: 688px) {
    .intro-article {
        padding-bottom: 40px;
        &__right {
            flex-direction: column;
        }
        &__marks {
            & p {
                padding: 12px 20px 12px 16px;
            }
        }
        &__form {
            padding: 18px 20px;
            &_title {
                font-size: 18px;
            }
        }
    }
}
