// advantages-1
.advantages {
    padding: 140px 0;
    border-radius: 30px;
    position: relative;
    overflow: hidden;

    &__bg {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__title {
        color: var(--fon-plashek-seryy);
    }

    &__btn {
        color: var(--fon-plashek-seryy);
        border: 1px solid var(--fon-plashek-seryy);
        &::after {
            background:  var(--tekst-temnyy);
        }
    }

    &__cards {
        display: flex;
        flex-wrap: wrap;
        gap: 20px;
    }

    &__card {
        width: calc(33% - 10px);
        border: 1px solid rgba(255, 255, 255, 0.2);
        border-radius: 20px;
        padding: 30px;
        color: var(--fon-plashek-seryy);

        &_top {
            display: flex;
            align-items: center;
            gap: 10px;
            font-family: var(--second-family);
            font-size: 20px;
            margin-bottom: 40px;
        }
    }
}


// media advantages-1
@media (max-width: 1050px) {
    .advantages {
        padding: 100px 0;
        &__card {
            width: calc(50% - 10px);
        }
    }
}
@media (max-width: 650px) {
    .advantages {
        padding: 60px 0;
        border-radius: 20px;
        &__cards {
            gap: 10px;
        }
        &__card {
            width: 100%;
            border-radius: 20px;
            padding: 20px;
            &_top {
                font-size: 16px;
                margin-bottom: 12px;
            }
            & img {
                width: 20px;
                height: 20px;
            }
        }
    }
}