// legal-docs-3
.legal-docs {

    &__top {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 40px;
        & a {
            display: block;
            width: fit-content;
        }
    }

    &__list {
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    &__item {
        border-radius: 20px;
        padding: 20px 30px;
        background: var(--fon-plashek-bezhevyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;

        &_name {
            flex: 1 0 35%;
            font-family: var(--second-family);
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }

        &_link {
            border-radius: 60px;
            padding: 10px 18px;
            background: var(--svetlo-korichnevyy);
            font-size: 14px;
            color: var(--tekst-temnyy);
            display: flex;
            align-items: center;
            gap: 10px;
            flex: 0 0 auto;
            transition: all 0.3s ease;
            &:hover {
                background: var(--svetlo-korichnevyy-2);
            }
        }

        &_info {
            display: flex;
            gap: 20px;
            font-size: 14px;
            color: var(--tekst-svetlyy);
            & span {
                color: var(--tekst-temnyy);
            }
            & p:first-child {
                padding-right: 20px;
                border-right: 1px solid var(--liniya);
            }
        }
    }
}



// media legal-docs-3
@media (max-width: 970px) {
    .legal-docs {
        &__item {
            flex-wrap: wrap;
            gap: 10px;
            &_name {
                flex: 1 0 60%;
            }
            &_info {
                flex: 1 0 100%;
                order: 1;
            }
        }
    }
}
@media (max-width: 688px) {
    .legal-docs {
        &__top {
            flex-direction: column;
            margin-bottom: 30px;
            & a {
                width: 100%;
            }
        }
        &__item {
            padding: 18px 20px;
            flex-direction: column;
            align-items: stretch;
            gap: 16px;
            &_link {
                justify-content: center;
                flex: 1 0 auto;
            }
            &_info {
                flex-direction: column;
                gap: 8px;
                margin-top: 10px;
                & p:first-child {
                    padding-right: 0;
                    border-right: none;
                }
            }
        }
    }
}