// error-4
.error {
    &__inner {
        padding-bottom: 50px;
        border-radius: 0 0 20px 20px;
        position: relative;
        overflow: hidden;
        margin-bottom: 50px;
    }

    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 100px;
    }

    &__number {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 300px;
        line-height: 120%;
        color: #fff;
        z-index: 10;
    }

    &__text {
        & p:first-child {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 40px;
            line-height: 120%;
            color: #fff;
            z-index: 10;
            position: relative;
            margin-bottom: 20px;
        }

        & .error__text-bottom {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: #fff;
            z-index: 10;
            position: relative;
            opacity: 0.8;
            margin-bottom: 40px;
        }

        & a {
            display: block;
            width: fit-content;
            background: white;
            &:hover {
                color: white;
            }
            &::after {
                background: var(--knopka);
                color: white;
            }
        }
    }

    &__links {
        & p {
            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            margin-bottom: 20px;
        }

        & ul {
            display: flex;
            flex-wrap: wrap;
            gap: 10px;

            & li {
                width: calc(100% / 3 - 8px);

                & a {
                    border-radius: 40px;
                    padding: 20px;
                    display: flex;
                    align-items: center;
                    gap: 20px;
                    height: 100%;
                    transition: all 0.3s ease;

                    border: 1px solid var(--liniya);

                    & span {
                        flex: 1 0 60%;
                        display: block;
                        font-family: var(--second-family);
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 140%;
                        color: var(--tekst-temnyy);
                    }

                    & img {
                        border-radius: 10px;
                        min-width: 60px;
                        width: 60px;
                        height: 60px;
                        object-fit: cover;
                    }

                    &:hover {
                        background: var(--svetlo-korichnevyy-2);
                    }
                }
            }
        }
    }
}
.error .btn-circle {
    background: var(--korichnevyy);
}

// media error-4
@media (max-width: 1100px) {
    .error {
        &__links ul li {
            width: calc(50% - 5px);
        }
        &__number {
            font-size: 200px;
            line-height: 1;
        }
        &__top {
            flex-direction: column;
            gap: 20px;
        }
        &__text a {
            margin: 0 auto;
        }
    }
}

@media (max-width: 800px) {
    .error {
        &__inner {
            padding-bottom: 60px;
            margin-bottom: 40px;
        }
        &__number {
            font-size: 130px;
            text-align: center;
        }
        &__text {
            width: 100%;
            min-width: auto;

            & p:first-child {
                font-size: 28px;
                line-height: 120%;
                color: #fff;
            }
            & p {
                margin-bottom: 17px;
            }
            & a {
                width: 100%;
            }
        }

        &__links {
            & p {
                margin-bottom: 30px;
            }
            & ul {
                gap: 14px;
                & li {
                    width: 100%;
                    & a {
                        padding: 16px 20px;
                        font-size: 16px;
                        line-height: 125%;
                    }
                }
            }
        }
    }
}