// filials-1
.filials__card_item {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
    opacity: 0.8;
}

.filials__card_content {
    z-index: 10;

}

.filials {
    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__card {

        padding: 30px;
        background: #f9f9fa;
        display: flex;
        flex-direction: column;
        align-items: start;
        justify-content: space-between;
        gap: 40px;
        position: relative;


        background: var(--belyy);
        border: 1px solid var(--liniya);
        border-radius: 30px;

        &_bg {
            position: absolute;
            top: 0;
            right: 0;
        }

        &_name {
            margin-bottom: 14px;

            font-family: var(--second-family);
            font-weight: 400;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }

        &_info {
            display: flex;
            flex-direction: column;
            gap: 20px;
            font-size: 14px;
        }

        &_phone {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 18px;
            line-height: 122%;
            color: var(--tekst-temnyy);
        }

        &_adres {
            font-weight: 500;
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst);
        }
    }
}


// media filials-1
@media (max-width: 1000px) {
    .filials {
        &__cards {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}

@media (max-width: 688px) {
    .filials {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }

        &__card {

            padding: 20px;
            gap: 30px;

            &_bg {
                max-height: 47%;
                top: 17px;
                object-fit: cover;
            }

            &_name {
                font-size: 18px;
            }

            &_btn {
                width: 100%;
                text-align: center;
                z-index: 10;
                padding: 8px;

                & span::after {
                    content: "";
                    display: inline-block;
                    width: 8px;
                    height: 8px;
                    border-top: 1px solid white;
                    border-right: 1px solid white;
                    transform: rotate(45deg);
                    margin-left: 8px;
                    transition: all 0.3s ease;
                }
            }

        }
    }
}