// feedback-1
.feedback {
    &__inner {
        display: flex;
        align-items: stretch;
        border-radius: 20px;
        overflow: hidden;
    }

    &__wrap {
        border: 1px solid var(--liniya);
        border-radius: 20px;
        padding: 40px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--belyy);
        display: flex;
        flex-direction: column;
        flex-basis: 55%;
        margin-right: -40px;
        position: relative;
        z-index: 2;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst-temnyy);
        margin-bottom: 8px;
    }

    &__subtitle {
        margin-bottom: 30px;
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 15px
    }

    &__input, &__textarea, &__select {
        border: 1px solid var(--liniya);
    }

    &__form-input__wrap {
        display: flex;
        gap: 15px;
        font-size: 14px;
        &>* {
            flex-basis: calc(50% - 7.5px);
        }
    }

    &__stars {
        display: flex;
        gap: 15px;
        padding-bottom: 14px;
        border-bottom: 1px solid var(--liniya);
        & p {
            font-size: 14px;
        }
    }

    &__btn {
        min-width: fit-content;
    }

    &__bottom-wrap {
        display: flex;
        align-items: center;
        gap: 15px;
    }

    &__picture {
        flex-basis: 45%;
        overflow: hidden;
        border-radius: 0 20px 20px 0;
        & img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            object-position: top;
        }
    }
}


// media feedback-1
@media (max-width: 1024px) {
    .feedback__inner {
        flex-direction: column;
        width: 100%;
    }
    .feedback__wrap {
        flex-basis: 100%;
        margin: 0;
    }
    .feedback__picture {
        flex-basis: 100%;
        max-height: 460px;
        width: 100%;
        margin-top: -20px;
        border-radius: 0  0 20px 20px;
    }
    .feedback__title {
        font-size: 30px;
    }
}
@media (max-width: 650px) {
    .feedback__form-input__wrap {
        flex-direction: column;
    }
    .feedback__bottom-wrap {
        flex-direction: column;
    }
    .feedback__btn {
        min-width: 100%;
    }
    .feedback__wrap {
        border-radius: 20px;
        padding: 20px;
    }
    .feedback__subtitle {
        font-size: 14px;
    }
}
@media (max-width: 550px) {
    .feedback__form-input__wrap, .feedback__form, .feedback__bottom-wrap {
        gap: 10px;
    }
    .feedback__inner {
        gap: 10px;
    }
    .feedback__stars {
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
    }
}
@media (max-width: 450px) {
    .feedback__picture {
        max-height: 300px;
    }
    .feedback__title {
        font-size: 24px;
    }
}