// popup-city-1
.popup-city {
    &__content {
        border-radius: 20px;
        padding: 70px;
        background: var(--belyy);
        max-width: 800px;
        min-height: 624px;
    }

    &__top {
        margin-bottom: 28px;
        display: flex;
        align-items: center;
    }

    &__title {
        font-family: var(--second-family);
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst-temnyy);
        padding-right: 20px;
        border-right: 1px solid #f0f5f6;
        margin-right: 20px;
    }

    &__city-active {
        display: block;
        border-radius: 60px;
        padding: 10px 20px;
        background: var(--knopka);
        font-size: 16px;
        text-align: center;
        color: var(--belyy);
        display: flex;
        align-items: center;
        gap: 8px;
        &::before {
            content: url("./../img/icons/location-white.svg");
            width: 18px;
            height: 18px;
        }
    }

    &__search {
        margin-top: 40px;
        &-result {
            max-height: 320px;
            overflow-y: auto;
        }
    }

    &__message {
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: var(--tekst-temnyy);
        margin-bottom: 20px;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        gap: 10px;
        & li a {
            display: block;
            border: 1px solid var(--knopka);
            border-radius: 60px;
            padding: 10px 20px;
            font-size: 16px;
            text-align: center;
            color: var(--knopka);
            transition: all 0.3s ease;
            &:hover {
                background-color: var(--svetlo-korichnevyy-2);
            }
        }
    }

    &__list_result {
        & li a {
            display: block;
            border-radius: 8px;
            padding: 14px 20px 14px 16px;
            font-size: 16px;
            color: var(--tekst-temnyy);
            display: flex;
            justify-content: space-between;
            gap: 20px;
            &:hover {
                background-color: var(--svetlo-korichnevyy-2);
            }
            & span:first-child {
                display: flex; 
                align-items: center;
                gap: 10px;
                &::before {
                    content: url("./../img/icons/location.svg");
                }
            }
            & span:last-child {
                font-size: 14px;
                color: var(--korichnevyy);
            }
        }
    }

    &__no-results-message {
        &::before {
            content: url("./../img/icons/no-location-1.svg");
        }
    }

    &__close {
        width: 34px;
        height: 34px;
        border-radius: 30px;
        border: 1px solid #e3edee;
    }
}


// media popup-city-1
@media (max-width: 688px) {
    .popup-city {
        &__content {
            border-radius: 10px;
            padding: 40px 20px 20px;
            min-height: 620px;
        }
        &__top {
            flex-direction: column;
            gap: 20px;
            align-items: stretch;
            margin-bottom: 12px;
        }
        &__title {
            font-size: 18px;
            text-align: left;
            padding: 0;
            margin: 0;
            border: none;
        }
        &__city-active {
            font-size: 14px;
            align-items: center;
            justify-content: center;
        }
        &__search {
            max-height: 345px;
            margin-top: 30px;
            &-result {
                max-height: 365px;
            }
        }
        &__message {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 4px;
        }
        &__list {
            flex-direction: column;
            flex-wrap: nowrap;
            gap: 0;
            max-height: 340px;
            overflow-y: auto;
            padding-right: 10px;
            &::-webkit-scrollbar {
                width: 4px;
                background-color: var(--liniya);
                padding: 5px;
            }
            &::-webkit-scrollbar-thumb {
                border-radius: 10px;
                background: var(--knopka);
            }
            & li a {
                display: block;
                border: none;
                border-bottom: 1px solid var(--liniya);
                border-radius: 0;
                padding: 16px 0;
                font-size: 14px;
                color: var(--knopka);
                text-align: left;
                &:hover {
                    color: var(--color-temno-zelenyy);
                }
            }
        } 
        &__list_result li a {
            flex-direction: column;
            gap: 2px;
            font-size: 14px;
            padding: 8px 20px 8px 16px;
            & span:last-child {
                font-size: 12px;
            }
        }
        &__close {
            width: 26px;
            height: 26px;
            top: 20px;
            right: 20px;
        }
    }
}


