// stages-4
.stages {
    &__cards {
        & ol {
            display: flex;
            flex-direction: column;
            gap: 10px;
            list-style: none;
            counter-reset: item;
            & li {
                border-radius: 20px;
                padding: 20px 20px 20px 30px;
                background: var(--fon-plashek-bezhevyy);
                display: flex;
                justify-content: space-between;
                align-items: center;
                gap: 20px;
                &::before {
                    content: counter(item, decimal-leading-zero); 
                    counter-increment: item;
                    display: block;
                    font-weight: 700;
                    font-size: 40px;
                    line-height: 120%;
                    color: var(--tekst-svetlyy);
                    opacity: 0.2;
                    flex-basis: 10%;
                }
            }
        }
    }

    &__name {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        color: var(--tekst-temnyy);
        display: flex;
        align-items: center;
        gap: 40px;
        flex-basis: 25%;
    }

    &__text {
        flex-basis: 50%;
    }

    &__img img {
        display: block;
        border-radius: 200px;
        min-width: 82px;
        width: 82px;
        height: 82px;
        object-fit: cover;
    }
}


// media stages-4
@media (max-width: 900px) {
    .stages {
        &__cards ol li {
            flex-wrap: wrap;
        }
        &__name {
            order: -1;
            flex-basis: 70%;
        }
        &__text {
            flex-basis: 100%;
        }
    }
}
@media (max-width: 620px) {
    .stages {
        &__title {
            margin-bottom: 20px;
        }
        &__cards ol li {
            padding: 20px;
            &::before {
                font-size: 16px;
                text-align: right;
            }
        }
        &__name {
            font-size: 16px;
            gap: 14px;
            flex: 1 0 80%;
        }
        &__img img {
            min-width: 60px;
            width: 60px;
            height: 60px;
        }
    }
}