// doctors-page-5
.doctors-page {
    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
}


// media doctors-page-5
@media (max-width: 1000px) {
    .doctors-page {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
    }
}

