// article-3
.article {
    &__block {
        &:not(:last-child) {
            margin-bottom: 50px;
        }
    }

    & h2, & h3, & h4, & h5, & h6 {
        font-family: var(--second-family);
        font-weight: 400;
        line-height: 140%;
        color: var(--tekst-temnyy);
        margin-bottom: 20px;
    }
    & h2 {
        font-size: 38px;
    }
    & h3 {
        font-size: 34px;
    }
    & h4 {
        font-size: 30px;
    }
    & h5 {
        font-size: 26px;
    }
    & h2:empty, & h3:empty, & h4:empty, & h5:empty, & h6:empty {
        margin-bottom: 0;
    }

    & p {
        &:not(:last-child) {
            margin-bottom: 20px;
        }
    }
    & span {
        font-weight: 500;
    }
    & a:not(.phone) {
        font-weight: 500;
        text-decoration: underline;
        color: var(--tekst-svetlyy);
        transition: all 0.3s ease;
        &:hover {
            color: var(--tekst-temnyy);
        }
    }

    & ul:not(.breadcrumbs) {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: "";
                display: block;
                min-width: 10px;
                width: 10px;
                height: 10px;
                border-radius: 100%;
                background-color: var(--tekst-temnyy);
                margin-top: 7px;
            }
        }
    }

    & ol {
        display: flex;
        flex-direction: column;
        gap: 15px;
        margin-bottom: 20px;
        list-style-type: none;
        counter-reset: item;
        & li {
            display: flex;
            gap: 10px;
            &::before {
                content: counter(item);
                counter-increment: item;
                display: block;
                min-width: 28px;
                width: 28px;
                height: 28px;
                border: 1px solid var(--tekst-svetlyy);
                border-radius: 20px;
                font-weight: 500;
                font-size: 12px;
                color: var(--tekst-svetlyy);
                display: flex;
                align-items: center;
                justify-content: center;
            }
        }
    }

    &__img {
        float: right;
        margin: 0 0 40px 40px;
        & img {
            border-radius: 20px;
            width: 100%;
            object-fit: cover;
        }
    }

    &__form {
        border-radius: 20px;
        padding: 40px 80px;
        background: var(--tekst-temnyy);
        color: var(--belyy);
        overflow: hidden;
        display: flex;
        gap: 20px;
        margin: 80px 0;
        &_title {
            font-weight: 600;
            font-size: 30px;
            line-height: 120%;
            color: var(--belyy);
        }
        &_phone {
            font-weight: 700;
            font-size: 18px;
            line-height: 111%;
            color: #fff;
            padding: 5px 0 5px 20px;
            border-left: 1px solid #fff;
        }
    }

    & table {
        display: block;
        width: 100%;
        border: none;
        border-collapse: collapse; 
        border: none;
        border-radius: 10px;
        margin-bottom: 20px;

        & thead {
            & th {
                border: none;
                padding: 20px 25px;
                background: var(--fon-plashek-seryy);
                font-weight: 500;
                font-size: 14px;
                line-height: 140%;
                color: var(--tekst-temnyy);
            }
            & tr th:first-child {
                border-radius: 10px 0 0 0;
            }
            & tr th:last-child {
                border-radius: 0 10px 0 0;
            }
        }

        & tbody {
            & td {
                border-right: 1px solid rgba(55, 56, 76, 0.1);
                padding: 20px;
                font-size: 14px;
                line-height: 140%;
                color: var(--tekst-svetlyy);
                vertical-align: top;
            }
            & tr td:last-child {
                border-right: none;
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;

        &_item {
            flex-grow: 1;
            border-radius: 60px;
            padding: 20px;
            background: var(--fon-plashek-seryy);
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 15px;
            & p {
                margin: 0 !important;
            }
        }

        &_links {
            display: flex;
            gap: 6px;
        }

        &_link {
            display: flex;
            justify-content: center;
            align-items: center;
            min-width: 26px;
            width: 26px;
            height: 26px;
            background: var(--tekst-temnyy);
            border-radius: 50%;
            transition: all 0.3s ease;
            &:hover {
                opacity: 0.6;
            }
        }
    }

    &__literature {
        &_title {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 30px;
        }
        & ol {
            & li {
                &::before {
                    min-width: none;
                    width: auto;
                    height: auto;
                    font-weight: 700;
                    font-size: 20px;
                    line-height: 100%;
                    color: var(--tekst-temnyy);
                    opacity: 0.4;
                    border: none;
                    align-items: flex-start;
                }
            }
        }
    }
}


// media article-3
@media (max-width: 1180px) {
    .article {
        &__form {
            margin: 0 0 20px 0;
            padding: 40px;
        }
    }
}
@media (max-width: 820px) {
    .article {
        &__img {
            & img {
                max-height: 400px;
            }
        }
        &__form {
            border-radius: 10px;
            padding: 20px;
            flex-direction: column;
            gap: 0;
            &_title {
                font-size: 22px;
            }
        }
    }
}
@media (max-width: 688px) {
    .article {
        &__block {
            display: flex;
            flex-direction: column;
            &:not(:last-child) {
                margin-bottom: 30px;
            }
        }
        & h2, & h3, & h4, & h5, & h6 {
            order: -2;
        }
        & h2 {
            font-size: 24px;
        }
        & h3 {
            font-size: 22px;
        }
        & h4 {
            font-size: 20px;
        }
        & h5 {
            font-size: 16px;
        }
        & ul li {
            &::before {
                min-width: 8px;
                width: 8px;
                height: 8px;
            }
        }
        &__img {
            order: -1;
            float: none;
            margin-left: 0;
            margin-bottom: 20px;
            & img {
                max-height: 230px;
            }
            & p {
                font-size: 12px;
            }
        }
        &__form {
            border-radius: 10px;
            padding: 20px;
            &_title {
                font-size: 22px;
                margin-bottom: 20px;
            }
        }
        & table {
            overflow-x: auto;
            max-width: 100%;
            & thead th, & tbody td {
                min-width: 243px;
            }
            &::-webkit-scrollbar {
                width: 0;
                height: 0;
            }
        }
        &__bottom {
            flex-direction: column;
            gap: 12px;
            &_item {
                padding: 12px 16px;
                font-size: 14px;
            }
        }
        &__literature {
            &_title {
                font-size: 20px;
                margin-bottom: 20px;
            }
            & ol {
                & li {
                    &::before {
                        font-size: 18px;
                    }
                }
            }
        }
    }
}