// prices-3
.prices {

    &__top {
        &_text {
            flex-basis: 50%;
        }
    }

    &__title {
        margin-bottom: 20px;
    }

    &__sort {
        display: flex;
        gap: 10px;
        margin-bottom: 40px;
        > * {
            flex: 1 0 30%;
        }
    }

    &__search {
        height: 100%;
        & input {
            height: 100%;
        }
    }

    &__table {
        display: block;
        width: 100%;

        &_body {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 12px;
        }

        &_item {
            width: 100%;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 40px;
            border-radius: 20px;
            padding: 30px;
            background: var(--fon-plashek-bezhevyy);
            color: var(--tekst-svetlyy);
        }

        &_name {
            flex: 1 0 60%;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }

        &_cost {
            flex: 1 0 16%;
            border-right: 1px solid var(--liniya);
            padding-right: 40px;
        }
        
        &_btn button {
            padding: 8px 16px;
        }
    }
}


// media prices-3
@media (max-width: 950px) {
    .prices {
        &__sort {
            flex-direction: column;
            gap: 12px;
            margin-bottom: 24px;
        }
        &__table {
            &_item {
                width: 100%;
                flex-direction: column;
                align-items: stretch;
                gap: 16px;
            }
            &_name {
                flex: 1 0 100%;
                padding-bottom: 16px;
                border-bottom: 1px solid var(--liniya);
            }
            &_cost {
                flex: 1 0 100%;
                border-right: none;
                padding-right: 0;
            }
        }
    }
}
@media (max-width: 688px) {
    .prices {
        &__table {
            &_item {
                padding: 20px;
            }
            &_name {
                font-size: 14px;
            }
        }
    }
}
@media (max-width: 500px) {
    .prices {
        &__table {
            &_cost span {
                display: block;
            }
            &_btn button {
                width: 100%;
            }
        }
    }
}





// page-prices-2
.page-prices {
    &__sort {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        margin-bottom: 50px;
        &_search {
            flex-basis: 25%;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px 24px;
    }

    &__card {
        background: var(--fon-plashek-bezhevyy);
        overflow: hidden;
        transition: all 0.5s ease;

        &_date {
            background: #fff;
        }
    }

    &__btn-arrow {
        display: none;
    }
}


// media page-prices-2
@media (max-width: 1100px) {
    .page-prices {
        &__sort {
            flex-direction: column;
            gap: 25px;
            margin-bottom: 30px;
        }
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 30px 24px;
        }
    }
}
@media (max-width: 688px) {
    .page-prices {
        &__sort {
            gap: 10px;
        }
        &__btn-arrow {
            display: block;
        }
        &__tab {
            &_btns {
                display: flex;
                flex-direction: column;
                gap: 0;
                max-height: 51px;
                overflow: hidden;
                margin-bottom: 0;
                transition: all 0.3s ease;
            }
            &_btns.acc-active {
                max-height: 400px;
                .page-prices__btn-arrow {
                    transform: rotate(180deg);
                }
            }
            &_btn {
                border-radius: 0;
                padding: 16px 0;
                background: transparent;
                text-align: left;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0 20px;
                .page-prices__btn-arrow {
                    opacity: 0;
                }
                &.active {
                    margin: 0;
                    order: -1;
                    border-radius: 30px;
                    padding: 16px 20px;
                    border: 1px solid var(--liniya);
                    .page-prices__btn-arrow {
                        opacity: 1;
                    }
                }
            }
        }
        &__btn-arrow {
            display: flex;
            margin: 0;
            &::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 4px;
            }
        }
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 12px;
        }
    }
}


