// tour
.tour__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.tour__item {
    position: relative;
	max-height: 207px;
    &:first-child {
        grid-row: span 2;
        grid-column: span 3;
		max-height: 434px;
        & .tour__item-descr {
            font-size: 26px;
            bottom: 32px;
            left: 40px;
        }
    }
}

.tour__item.zoom::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(55, 56, 76, 0.8);
    border-radius: 20px;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 2;
}

.tour__item.zoom::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
	width: 95px;
	height: 50px;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background: url("./../img/help/tour3D.svg") no-repeat center center / cover;
    opacity: 0;
    transition: opacity 0.3s ease;
    z-index: 3;
}

.tour__item.zoom:hover::before {
    opacity: 1;
}
.tour__item.zoom:hover::after {
    opacity: 1;
}
.tour__item.zoom:hover .tour__item-descr {
    opacity: 1;
}


.tour__item-picture {
    border-radius: 20px;
    position: relative;
    overflow: hidden;

    & img {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.tour__item-descr {
	font-family: var(--second-family);
	font-weight: 400;
	font-size: 18px;
	line-height: 140%;
	color: #fff;
    position: absolute;
    bottom: 20px;
    left: 24px;
    right: 24px;
    z-index: 3;
    height: auto;
    transition: opacity 0.3s ease;
	opacity: 0;
}


// media tour
@media (max-width: 1100px) {
    .tour__inner {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }
    .tour__item {
        position: relative;
        &:first-child {
            grid-row: span 1;
            grid-column: span 1;
			max-height: 207px;
            & .tour__item-descr {
                font-size: 22px;
                bottom: 20px;
                left: 24px;
            }
        }
    }
}
@media (max-width: 850px) {
    .tour__inner {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
}

@media (max-width: 600px) {
    .tour__title-h1 {
        margin-bottom: 30px;
    }
    .tour__inner {
        grid-template-columns: repeat(1, 1fr);
        gap: 12px;
    }
	.tour__item.zoom::after {
		width: 80px;
		height: 45px;
	}
	
    .tour__item:nth-child(n) {
		max-height: 320px;
		& .tour__item-descr {
			font-size: 16px;
		}
    }
}

