// doctor-page-2
.doctor-page {
    padding-top: 50px;

    &__flex {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__block {
        border-radius: 20px;
        padding: 40px;
        background: var(--fon-plashek-bezhevyy);

        &_title {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 30px;
        }
    }

    &__info {
        display: flex;
        gap: 20px;
        padding: 0;

        &_photo {
            flex: 1 0 30%;
            display: block;
            width: 100%;
            max-width: 427px;
            max-height: 427px;
            position: relative;
            overflow: hidden;
            border-radius: 20px;
        }


        &_description {
            font-size: 14px;
            font-weight: 400;
            line-height: 19.6px;
            text-align: left;
            
        }

        &_img {
            display: block;
            width: 100%;
            height: 100%;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &_content {
            flex: 1 0 60%;
            padding: 32px 34px;
            display: flex;
            flex-direction: column;
            gap: 30px;
            background: var(--fon-plashek-bezhevyy);
            border-radius: 20px;
        }

        &_experience {
            border-radius: 60px;
            padding: 8px 16px;
            background: var(--belyy);
            font-weight: 500;
            font-size: 14px;
            color: var(--tekst-svetlyy);
            width: fit-content;
            margin-bottom: 10px;
        }

        &_fio {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 40px;
            line-height: 120%;
            color: var(--tekst-temnyy);
        }

        &_post {
            display: flex;
            gap: 30px;
        }

        &_btns {
            display: flex;
            gap: 10px;
        }

        &_btns--mob {
            display: none;
        }

        &_price {
            border-radius: 60px;
            padding: 14px 20px;
            background: var(--svetlo-korichnevyy-2);
            font-size: 14px;
            line-height: 140%;
            text-align: center;
            color: var(--tekst-svetlyy);

            & span {
                text-wrap: nowrap;
                font-size: 16px;
                font-weight: 700;
                line-height: 22.4px;
                text-align: left;
            }
        }

        &_bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 20px;
        }
    }

    &__post {
        &_item {
            font-size: 14px;
            line-height: 140%;
            display: flex;
            gap: 10px;

            &:first-child {
                padding-right: 30px;
                border-right: 1px solid #4b4c5e;
            }
        }

        &_icon {
            display: block;
            border-radius: 100px;
            padding: 8px;
            min-width: 43px;
            width: 43px;
            height: 43px;
            background: var(--svetlo-korichnevyy-2);
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &_text {
            margin-bottom: 5px;
            font-size: 14px;
            font-weight: 400;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            opacity: 0.8;
        }

        &_post {
            font-size: 14px;
            font-weight: 500;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }
    }

    &__rating {
        position: absolute;
        bottom: 16px;
        left: 16px;
        right: 16px;
        border-radius: 30px;
        padding: 10px 14px;
        backdrop-filter: blur(9.600000381469727px);
        background: rgba(98, 91, 90, 0.6);
        display: flex;
        align-items: center;
        justify-content: center;

        &_estimation {
            padding-right: 16px;
            border-right: 1px solid #c7d6dc;
            margin-right: 16px;
            display: flex;
            align-items: center;
            gap: 5px;

            & p {
                font-family: var(--second-family);
                font-weight: 400;
                font-size: 26px;
                line-height: 140%;
                color: #fff;
            }
        }

        &_stars {
            display: flex;
            gap: 2px;
        }

        &_star {
            opacity: 0.6;
        }
    }

    &__schedule {
        &_list {
            display: flex;
        }

        &_item {
            flex-grow: 1;
            color: var(--tekst);
            display: flex;
            flex-direction: column;
            gap: 6px;
            align-items: center;
            justify-content: center;
            border-radius: 20px;
            padding: 14px 20px;

            &:nth-child(odd) {
                background: #F3EEE9;
            }
        }

        &_time {
            font-weight: 500;
            color: #37384C;
            font-size: 14px;
            font-weight: 500;
            line-height: 19.6px;
            text-align: left;
        }

        &_weekend {
            color: #625B5A;
            font-size: 14px;
            font-weight: 700;
            line-height: 19.6px;
            text-align: left;
        }
    }

    &__list ul {
        display: flex;
        flex-direction: column;
        gap: 14px;

        & li {
            display: flex;
            gap: 12px;
            color: var(--tekst);

            &::before {
                content: "";
                display: block;
                border-radius: 20px;
                min-width: 18px;
                width: 18px;
                height: 3px;
                background-color: #625B5A;
                margin-top: 9px;
            }

            & p {
                color: #656675;
            }

            & span {
                font-weight: 600;
                color: #625B5A;
                font-size: 16px;
                font-weight: 600;
                line-height: 22.4px;
                text-align: left;
            }
        }
    }

    &__diplomas {
        &_swiper {
            margin-bottom: 20px;
            height: auto;
        }

        &_swiper-slide {
            border-radius: 10px;
            overflow: hidden;
            background: var(--belyy);

            & a {
                display: block;
                border-radius: 10px;
                overflow: hidden;
                background: var(--svetlo-korichnevyy-2);

                & p {
                    padding: 20px;
                    width: 100%;
                    color: var(--tekst);
                    background: var(--svetlo-korichnevyy-2);
                    transition: all 0.8s ease;
                    border-radius: 20px 20px 20px 20px;


                    font-family: var(--font-family);
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 140%;
                    color: var(--tekst-svetlyy);
                }
            }

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &_swiper-btns {
            display: none;
        }

        &_picture {
            display: block;
            border-radius: 20px;
            overflow: hidden;
        }
    }
}

.doctor-main {
    & .doctor-page__info {
        &_content {
            background: var(--tekst-temnyy);
            > * {
                color: #fff;
            }
        }
        &_experience {
            background: rgba(255, 255, 255, 0.1);
            color: var(--belyy);
        }
        &_fio {
            color: var(--belyy);
        }
        &_price {
            background: rgba(255, 255, 255, 0.1);
            color: var(--belyy);
        }
        &_btn {
            background: var(--belyy);
            color: var(--tekst-temnyy);
        }
    }
    & .doctor-page__post {
        &_item {
            &:first-child {
                padding-right: 40px;
                border-right: 1px solid rgba(255, 255, 255, 0.2);
            }
        }
        &_icon {
            background: #fff;
        }
        &_text, &_post {
            color: var(--belyy);
        }
    }
}

// media doctor-page
@media (max-width: 1000px) {
    .doctor-page {
        &__info {
            display: contents;
            &_photo {
                margin: 0 auto;
                max-width: 389px;
            }
            &_content {
                border-radius: 20px;
                padding: 40px;
            }
            &_btns {
                display: none;
            }
            &_btns--mob {
                display: flex;
                justify-content: center;
                gap: 20px;
                & > * {
                    flex-grow: 1;
                }
            }
            &_post {
                flex-direction: column;
                gap: 14px;
            }
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
                border-radius: 20px;
            }
        }

        &__post {
            &_item:first-child {
                padding-right: 0;
                border-right: none;
                padding-bottom: 14px;
                border-bottom: 1px solid #c7d6dc;
            }
        }

        &__schedule {
            &_list {
                flex-direction: column;
            }
            &_item {
                padding: 12px 20px;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                gap: 16px;
            }
        }
    }

    .doctor-main {
        & .doctor-page__info {
            &_price {
                background: var(--svetlo-korichnevyy-2);
                color: var(--tekst-svetlyy);
            }
        }
        & .doctor-page__post {
            &_item:first-child {
                padding-right: 0;
                border-right: none;
                padding-bottom: 14px;
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            }
        }
    }
}

@media (max-width: 688px) {
    .doctor-page {
        &__block {
            border-radius: 20px;
            padding: 20px;
            background-color: #F9F7F6;

            &_title {
                font-family: var(--second-family);
                font-weight: 400;
                font-size: 26px;
                line-height: 140%;
                color: var(--tekst-temnyy);
            }
        }
        &__photo {
            max-width: 350px;
            max-height: 350px;
        }

        &__rating {
            &_estimation {
                padding-right: 10px;
                margin-right: 10px;
                & p {
                    font-size: 18px;
                }
            }
        }

        &__info {
            gap: 20px;
            &_content {
                border-radius: 20px;
                padding: 20px;
            }
            &_fio {
                font-size: 20px;
            }
            &_price {
                text-align: center;
            }
            &_btns--mob {
                flex-direction: column-reverse;
                gap: 10px;
            }
        }

        &__post {
            &_icon {
                min-width: 30px;
                width: 30px;
                height: 30px;
                padding: 5px;
            }
        }

        &__diplomas {
            &_swiper {
                margin-bottom: 20px;
            }
            &_swiper-slide {
                & a {
                    border-radius: 10px;
                    & p {
                        border-radius: 10px;
                    }
                }
                & img {
                    border-radius: 10px;
                }
            }
    
            &_swiper-btns {
                display: flex;
                justify-content: center;
            }
        }
    }
}