// stocks-7
.stocks {
    padding: 140px 0;
    position: relative;
    overflow: hidden;

    &__row {
        display: flex;
        justify-content: space-between;
        gap: 60px;
    }

    &__top {
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        margin-bottom: 28px;
    }

    &__title {
        font-size: 22px;
        line-height: 140%;
        color: var(--belyy);
    }

    &__btn {
        border: 1px solid var(--belyy);
        color: var(--belyy);
        background: transparent;
    }

    &__swiper-btns {
        display: flex;
        align-items: center;
        gap: 20px;
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 3;
        max-width: 387px;
        width: 100%;
    }

    &__swiper-scrollbar {
        width: 100%;
    }

    &__swiper-pagination {
        width: max-content;
        text-wrap: nowrap;
    }

    &__swiper-slide {
        height: auto;
        opacity: 0 !important;
        &.swiper-slide-active {
            opacity: 1 !important;
        }
    }

    &__cards {
        height: 100%;
        max-width: 800px;
    }

    &__card {
        overflow: hidden;
        position: relative;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 140px;

        &_inner {
            max-width: 85%;
            height: 100%;
        }

        &_date {
            border-radius: 100px;
            padding: 8px 16px;
            background: rgba(249, 247, 246, 0.2);
            font-size: 14px;
            color: var(--fon-plashek-seryy);
            width: max-content;
            margin-bottom: 20px;
            & span:first-child {
                padding-right: 10px;
                margin-right: 10px;
                border-right: 1px solid rgba(255, 255, 255, 0.5);
            }
        }

        &_title {
            font-family: var(--second-family);
            font-size: 38px;
            line-height: 140%;
            color: var(--fon-plashek-seryy);
            margin-bottom: 10px;
        }

        &_description {
            font-size: 14px;
            color: var(--fon-plashek-seryy);
        }

        &_btn {
            font-size: 16px;
            color: var(--belyy);
            &::after {
                border-top: 1px solid var(--belyy);
                border-right: 1px solid var(--belyy);
            }
        }
    }
    &__picture {
        
        & img {
            width: 100%;
            height: 100%;
            border-radius: 20px;
            object-fit: cover;
        }
    }
}




// media stocks-7
@media (max-width: 1140px) {
    .stocks {
        &__cards {
            max-width: 560px;
        }
    }
}
@media (max-width: 860px) {
    .stocks {
        padding: 100px 0;
        &__row {
            flex-direction: column;
            gap: 20px;
        }
        &__swiper-btns {
            min-width: 100%;
            justify-content: center;
        }
        &__swiper-pagination {
            display: none;
        }
        &__swiper-scrollbar {
            display: none;
        }
        &__cards {
            max-width: 100%;
        }
        &__card {
            gap: 20px;
            padding-bottom: 70px;
            &_inner {
                max-width: 100%;
            }
            &_date {
                width: 100%;
                display: flex;
                justify-content: space-around;
                margin-bottom: 30px;
                & span {
                    flex: 1 0 40%;
                    text-align: center;
                }
            }
            &_title {
                font-size: 24px;
                margin-bottom: 14px;
            }
            &_btn {
                font-size: 14px;
            }
        }
        &__picture {
            order: 2;
            & img {
                max-height: 420px;
                object-position: top;
            }
        }
    }
    
}
@media (max-width: 800px) {
    .stocks {
        &__flex {
            display: contents;
        }
        &__title {
            font-size: 24px;
            padding-bottom: 20px;
            border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        }
    }
    
}
@media (max-width: 600px) {
    .stocks {
        padding: 60px 0;
        &__picture {
            & img {
                max-height: 300px;
            }
        }
    }
    
}
