// licenses-5
.licenses {
    padding: 140px 0;
    position: relative;
    overflow: hidden;
    border-radius: 0 0 20px 20px;
    color: #fff;

    &__flex {
        display: flex;
        justify-content: space-between;
        gap: 50px;
    }

    &__content {
        flex-basis: 700px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 40px;
    }

    &__top {
        & p:not(:last-child) {
            margin-bottom: 20px;
        }
    }

    &__title {
        margin-bottom: 30px;
        color: #fff;
    }

    &__bottom {
        border-radius: 20px;
        padding: 40px;
        backdrop-filter: blur(20px);
        background: rgba(65, 66, 86, 0.6);
        position: relative;
        & p {
            font-weight: 600;
            font-size: 18px;
            line-height: 140%;
            margin-bottom: 26px;
            position: relative;
            z-index: 2;
        }
        &_icon {
            position: absolute;
            bottom: 31px;
            right: 107px;
        }
    }

    &__links {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 20px;
        position: relative;
        z-index: 2;
    }

    &__link {
        font-weight: 500;
        font-size: 16px;
        line-height: 140%;
        color: var(--belyy);
        display: flex;
        align-items: center;
        gap: 10px;
        transition: all 0.3s ease;
        &:hover {
            opacity: 0.6;
        }
    }

    &__images {
        width: 100%;
        max-width: 650px;
    }

    &__swiper {
        height: auto;
        position: relative;
        margin-bottom: 20px;
        max-width: 700px;
    }
    
    &__swiper-slide {
        position: relative;
        border-radius: 20px;
        transition: all 0.3s ease;
        z-index: 1;
        &:after {
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            background-color: rgba(255, 255, 255, 0.8);
            position: absolute;
            top: 0;
            left: 0;
            transition: all 0.6s ease;
        }
    
        &.swiper-slide-active {
            z-index: 3 !important;
            &:after {
                background-color: rgba(255, 255, 255, 0);
                z-index: -1;
            }
        }
    
        &.swiper-slide-next {
            z-index: 2 !important;
            &:after {
                background-color: rgba(255, 255, 255, 0.4);
            }
        }
        &.swiper-slide-prev {
            opacity: 0;
        }
    }
    
    &__picture {
        width: 100%;
        height: 100%;
        display: block;
    }

    &__image {
        display: block;
        border-radius: 20px;
        overflow: hidden;
        width: fit-content;
        position: relative;
        width: 100%;
        & img {
            display: block;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        &_label {
            position: absolute;
            bottom: 14px; 
            left: 14px;
            right: 14px;
            border-radius: 10px;
            padding: 10px 20px;
            background: var(--belyy);
            font-size: 14px;
            line-height: 130%;
            text-align: center;
            color: var(--tekst-temnyy);
        }
    }

    &__swiper-btns {
        gap: 30px;
        justify-content: center;
        max-width: 372px;
        margin: 0 auto;
    }
}

.licenses-services {
    padding: 0;
    color: var(--tekst-temnyy);
    & .licenses__title {
        color: var(--tekst-temnyy);
    }
    & .licenses__subtitle {
        color: var(--tekst-svetlyy);
    }
    & .licenses__bottom {
        backdrop-filter: blur(20px);
        background: var(--fon-plashek-bezhevyy);
    }
    & .licenses__link {
        color: var(--tekst-temnyy);
    }
    & .licenses__swiper-scrollbar {
        background: var(--svetlo-korichnevyy-2);
    }
}

// media licenses-5
@media (max-width: 1140px) {
    .licenses {
        padding: 100px 0;
        &__flex {
            flex-direction: column;
            gap: 30px;
        }
        &__content {
            display: contents;
        }
        &__images {
            max-width: 100%;
        }
        &__bottom {
            order: 3;
        }
        &__swiper-slide {
            background-position: top;
        }
    }
    .licenses-services {
        padding: 0;
    }
}
@media (max-width: 688px) {
    .licenses {
        padding: 60px 0;
        overflow: hidden;
        &__swiper {
            overflow: visible;
        }
        &__swiper-slide {
            min-width: 215px;
        }
        &__bottom {
            border-radius: 20px;
            padding: 20px;
            &_icon {
                bottom: 15px;
                right: 15px;
            }
        }
        &__links {
            flex-direction: column;
            align-items: start;
            gap: 10px;
        }
        &__link {
            font-size: 14px;
        }
        &__image {
            &_label {
                font-size: 12px;
            }
        }
        &__swiper-scrollbar {
            display: none;
        }
    }
    .licenses-services {
        padding: 0;
    }
}


