// jobs-2
.jobs {
    &__list {
        display: flex;
        flex-direction: column;
        gap: 12px;
        list-style-type: none;
        counter-reset: item;
    }

    &__item {
        padding: 0px 40px 0px 40px;
        border: 1px solid var(--liniya);
        border-radius: 20px;
    }

    &__button {

        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 40px;
        padding-top: 30px;
        padding-bottom: 30px;

        &.accordion--active {
            margin-bottom: 10px;
            & .jobs__button_btn-more svg path:last-child {
                opacity: 0;
            }
        }

        &_text {
            flex: 1 0 70%;
            display: flex;
            align-items: center;
            gap: 20px;

        }

        &_name {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            text-align: left;
        }

        &_salary {
            font-family: var(--font-family);
            font-weight: 500;
            font-size: 18px;
            line-height: 140%;
            text-align: center;
            color: var(--knopka);
            background: var(--fon-plashek-bezhevyy);
            border-radius: 40px;
            padding: 10px 20px;
        }

        &_btn-more svg path {
            transition: all 0.3s ease;
        }
    }

    &__content {
        max-height: 0;
        overflow: hidden;
        padding: 0;
        display: flex;
        flex-direction: column;
        gap: 30px;


        &.accordion__content--active {
            margin-bottom: 24px;
        }

        &_labels {
            display: flex;
            gap: 25px;
            width: fit-content;
        }

        &_label {
            display: flex;
            align-items: center;
            gap: 15px;

            & p:first-child {
                font-family: var(--font-family);
                font-weight: 600;
                font-size: 16px;
                line-height: 140%;
                color: var(--tekst-temnyy);
                margin-bottom: 2px;
            }

            & p:last-child {
                opacity: 0.8;
                font-family: var(--font-family);
                font-weight: 400;
                font-size: 14px;
                line-height: 140%;
                color: var(--tekst-svetlyy);
            }

            &:not(:last-child) {
                padding-right: 25px;
                border-right: 1px solid rgba(97, 97, 97, 0.2);
            }
        }

        &_icon {
            border-radius: 100px;
            padding: 8px;
            width: 36px;
            height: 36px;
            background: var(--fon-plashek-bezhevyy);
            display: flex;
            justify-content: center;
            align-items: center;
        }

        &_block {
            & p {
                margin-bottom: 15px;
                font-family: var(--font-family);
                font-weight: 600;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst-temnyy);
            }

            & ul {
                display: flex;
                flex-direction: column;
                gap: 12px;

                & li {
                    display: flex;
                    gap: 10px;
                    font-family: var(--font-family);
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 140%;
                    color: var(--tekst-svetlyy);

                    &::before {
                        content: "";
                        display: block;
                        margin-top: 7px;
                        background: var(--knopka);
                        border-radius: 10px;
                        min-width: 18px;
                        width: 18px;
                        height: 3px;
                    }
                }
            }
        }

        &_bottom {
            padding: 30px 40px;
            display: flex;
            align-items: center;
            gap: 40px;
            border-radius: 20px;
            padding: 30px 40px;
            background: var(--fon-plashek-bezhevyy);
        }

        &_text {
            flex: 1 0 50%;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }

        &_contacts {
            flex: 1 0 40%;
            display: flex;
            flex-direction: column;
            gap: 6px;

            font-family: var(--font-family);
            font-weight: 400;
            font-size: 16px;
            line-height: 140%;
            color: var(--tekst-svetlyy);

            & a {
                text-wrap: nowrap;
                font-weight: 700;
                color: var(--tekst-temnyy);
            }
        }
    }
}


// media jobs-2
@media (max-width: 740px) {

    .jobs__content_block ul li {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: var(--knopka);
    }

    .jobs__content_contacts {
        font-size: 14px;
    }

    .jobs {
        &__list {
            gap: 30px;
        }

        &__item {
            padding: 0px 20px 0px 20px;
        }

        &__button {
            padding: 0;
            padding-top: 20px;
            padding-bottom: 20px;
            align-items: start;

            &.accordion--active {
                margin-bottom: 0;
            }

            &::before {
                display: none;
            }

            &_text {
                flex-direction: column;
                gap: 12px;
                align-items: start;
            }

            &_name {
                text-align: start;
                font-size: 18px;
                line-height: 140%;
                color: var(--tekst-temnyy);
            }

            &_salary {
                padding: 8px 12px;
                font-size: 16px;
                font-weight: 700;
            }
        }

        &__content {
            padding: 0;

            &_labels {
                flex-direction: column;
                gap: 14px;
                width: 100%;
            }

            &_label:not(:last-child) {
                padding-right: 0;
                border-right: none;
                padding-bottom: 14px;
                border-bottom: 0px solid #c7d6dc;
            }

            &_block {
                & ul li::before {
                    min-width: 12px;
                    width: 12px;
                    height: 2px;
                    margin-top: 8px;
                }
            }

            &_bottom {
                padding: 20px;
                flex-direction: column;
                align-items: stretch;
                gap: 14px;
            }

            &_text {
                font-size: 16px;
            }
        }
    }
}