// footer-7
.footer {
    &__plashka {
        padding: 12px 0px;
        border-radius: 20px 20px 0 0;
        background: var(--svetlo-korichnevyy);
        font-weight: 500;
        font-size: 14px;
        line-height: 140%;
        text-align: center;
        color: var(--tekst-svetlyy);
    }

    &__inner {
        padding: 80px 0;
        background: var(--tekst-temnyy);
    }

    &__separator {
        padding-bottom: 40px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.2);
        margin-bottom: 40px;
    }

    &__btn-up {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        font-weight: 500;
        font-size: 14px;
        color: var(--color-temno-zelenyy);
        & div {
            width: 40px;
            height: 40px;
            background: var(--belyy);
            &::after {
                width: 10px;
                height: 10px;
                border-top: 1px solid var(--color-temno-zelenyy);
                border-right: 1px solid var(--color-temno-zelenyy);
                transform: rotate(-45deg);
                margin: 4px 0 0 0;
            }
        }
    }

    &__top {
        &_logo {
            max-width: 122px;
            width: 100%;
        }

        &_row {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 30px;
        }

        &_left {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &_logo, &_search-btns {
            padding-right: 20px;
            border-right: 1px solid rgba(255, 255, 255, 0.2);
        }

        &_networks {
            display: flex;
            gap: 6px;
            & a {
                display: block;
                width: 30px;
                height: 30px;
                padding: 6px;
                border-radius: 100px;
                background-color:  var(--svetlo-korichnevyy);
                border: 1px solid transparent;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.4s ease;
                & svg path {
                    transition: all 0.4s ease;
                }
                &:hover {
                    background: transparent;
                    border: 1px solid var(--svetlo-korichnevyy);
                    & svg path {
                        fill: var(--svetlo-korichnevyy);
                    }
                }
            }
        }

        &_right {
            display: flex;
            align-items: center;
            gap: 20px;
            //margin-right: -70px;
        }

        &_search-btns {
            display: flex;
            align-items: center;
            gap: 20px;
        }

        &_btn-search {
            display: flex;
            align-items: center;
            gap: 10px;
            font-weight: 500;
            line-height: 1;
            color: var(--svetlo-korichnevyy);
            border: 1px solid var(--svetlo-korichnevyy);
            border-radius: 60px;
            padding: 16px 20px;
            opacity: 0.6;
            transition: all 0.3s ease;
            &:hover {
                opacity: 1;
            }
        }

        &_btn-eye {
            transition: all 0.3s ease;
            opacity: 0.6;
            &:hover {
                opacity: 1;
            }
            &--mobile {
                display: none;
            }
        }

        &_btns {
            display: flex;
            gap: 12px;
            & button:first-child {
                border: 1px solid var(--svetlo-korichnevyy);
                color: var(--svetlo-korichnevyy);
                background: transparent;
            }
            & button:last-child {
                background: var(--svetlo-korichnevyy);
                color: var(--knopka);
            }
        }
    }


    &__info {
        &_row {
            display: flex;
            justify-content: space-between;
            gap: 20px;
        }

        &_item {
            & p:first-child {
                font-size: 14px;
                color: #fff;
                opacity: 0.6;
                margin-bottom: 8px;
            }
        }

        &_text {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            & * {
                font-weight: 500;
                font-size: 16px;
                line-height: 140%;
                color: #fff;
            }
            & span {
                display: block;
                font-weight: 400;
            }
            & a:hover, & button:hover {
                text-decoration: underline;
            }
        }

        &_phone {
            & a {
                font-size: 22px;
            }
        }
    }


    &__nav {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 40px;

        &_item {
            flex-basis: 23%;
            & p {
                font-weight: 700;
                font-size: 18px;
                line-height: 140%;
                color: #fff;
                margin-bottom: 10px;
            }
            & ul {
                display: flex;
                flex-direction: column;
                gap: 10px;
                & a {
                    color: #fff;
                    opacity: 0.8;
                    transition: all 0.3s ease;
                    &:hover {
                        opacity: 1;
                    }
                }
            }
        }

        &_item-menu {
            & ul {
                display: block;
                columns: 2;
                column-gap: 20px;
                & a {
                    display: block;
                    font-weight: 700;
                    font-size: 18px;
                    color: #fff;
                    margin-bottom: 24px;
                }
            }
        }
    }


    &__bottom {
        display: flex;
        justify-content: space-between;
        gap: 20px;

        &_links {
            display: flex;
            flex-direction: column;
            gap: 10px;
            & a {
                text-decoration: underline;
                color: #fff;
                opacity: 0.8;
                &:hover {
                    opacity: 1;
                }
            }
        }

        &_pays {
            display: flex;
            gap: 10px;
            margin-top: 12px;
            & img {
                display: block;
                width: 80px;
                height: 53px;
                padding: 17px 28px;
                border-radius: 10px;
                background-color: var(--svetlo-korichnevyy);
            }
        }
    }

    &__copy {
        font-size: 12px;
        line-height: 140%;
        color: #fff;
        opacity: 0.6;
        & p:first-child {
            margin-bottom: 10px;
        }
    }
}


// media footer-7
@media (max-width: 1480px) {
    .footer {
        &__top_right {
            margin: 0;
        }
    }
}
@media (max-width: 1185px) {
    .footer {
        &__info {
            &_row {
                flex-wrap: wrap;
                gap: 20px;
            }
            &_item {
                flex: 1 0 45%;
            }
        }
        &__nav_item {
            flex-basis: 45%;
        }
    }
}
@media (max-width: 850px) {
    .footer {
        &__plashka {
            font-size: 12px;
            border-radius: 0;
        }
        &__inner {
            padding: 20px 0;
        }
        &__separator {
            padding-bottom: 30px;
            margin-bottom: 30px;
        }

        &__top {
            &_row {
                flex-direction: column;
                gap: 14px;
            }
            &_left {
                width: 100%;
                justify-content: space-between;
            }
            &_logo {
                flex-basis: 45%;
                padding: 0;
                border: none;
            }
            &_right {
                flex-direction: column;
                align-items: stretch;
                width: 100%;
                gap: 30px;
            }
            &_search-btns {
                padding: 0;
                border: none;
            }
            &_btn-search {
                width: 100%;
                justify-content: center;
            }
            &_btns {
                flex: 1 0 100%;
                flex-direction: column;
            }
            &_btn-eye {
                display: none;
            }
            &_btn-eye--mobile {
                display: block;
                position: absolute;
                bottom: 0;
                right: 0;
            }
        }
        &__info {
            &_row {
                flex-direction: column;
                gap: 16px;
            }
            &_item {
                & p:first-child {
                    font-size: 12px;
                }
                &:last-child {
                    order: -1;
                }
            }
            &_text {
                align-items: center;
                & > * {
                    font-size: 14px;
                    font-weight: 500;
                }
                & picture {
                    display: none;
                }
                & button, & span {
                    display: inline-block;
                    font-size: 14px;
                    font-weight: 500;
                }
            }
            &_phone a {
                font-size: 18px;
            }
        }
        &__nav {
            gap: 20px;
            &_item {
                flex-basis: 100%;
                & p {
                    font-weight: 500;
                    font-size: 16px;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin: 0;
                    transition: all 0.4s ease;
                    &::after {
                        content: "";
                        display: inline-block;
                        width: 8px;
                        height: 8px;
                        border-top: 1px solid #fff;
                        border-right: 1px solid #fff;
                        transform: rotate(135deg);
                        transition: all 0.3s ease;
                    }
                    &.active {
                        margin-bottom: 12px;
                        &::after {
                            transform: rotate(315deg);
                        }
                    }
                }
                & ul {
                    max-height: 0;
                    overflow: hidden;
                    transition: all 0.3s ease;
                    &.active {
                        max-height: 500px;
                    }
                }
            }
            &_item-menu {
                padding-top: 30px;
                border-top: 1px solid rgba(85, 100, 115, 0.1);
                margin-top: 10px;
                & ul {
                    column-gap: 80px;
                    max-height: none;
                    & a {
                        font-weight: 500;
                        font-size: 16px;
                        margin-bottom: 20px;
                    }
                }
            }
        }
    
        &__bottom {
            flex-direction: column-reverse;
            gap: 20px;
            &_links {
                position: relative;
                & a {
                    font-size: 14px;
                    width: 80%;
                }
            }
            &_pays {
                & img {
                    height: 45px;
                    padding: 10px 28px;
                }
            }
        }
    }
    
}