// legal-info-4
.legal-info__title {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 22px;
    line-height: 140%;
    color: var(--tekst-temnyy);
    margin-bottom: 20px;
}

.legal-info__supervision-adress {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 130%;
    color: #83838f;
}

.legal-info__green-block p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 130%;
    color: var(--tekst-svetlyy);
    font-weight: 500;
    color: var(--tekst-temnyy);
}


.legal-info__green-block a {
    font-family: var(--second-family);
    color: var(--knopka);
    text-decoration: none;
}

.legal-info {
    &__inner {
        display: flex;
        gap: 40px;
    }

    &__left {
        flex: 1 0 62%;
        padding-right: 40px;
        border-right: 1px solid var(--color-svetlo-zelenyy-2);
    }

    &__right {
        flex: 1 0 30%;
    }

    &__block {
        margin-bottom: 60px;
    }

    &__accordion {
        display: contents;
    }

    &__btn-more {
        display: none;
    }

    &__supervisions {
        display: flex;
        flex-direction: column;
        gap: 8px;
    }

    &__supervision {
        & p {
            &:not(:last-child) {
                margin-bottom: 8px;
            }

            &:first-child {
                padding-bottom: 10px;
                border-bottom: 1px solid #E7E6EA;
                margin-bottom: 16px;

                font-family: var(--second-family);
                font-weight: 400;
                font-size: 16px;
                line-height: 140%;
                color: var(--tekst-temnyy);
            }
        }
    }

    &__green-block {
        border-radius: 20px;
        padding: 30px;
        background: var(--fon-plashek-bezhevyy);
    }
}


// media legal-info-4
@media (max-width: 1000px) {
    .legal-info {

        &__inner,
        &__left,
        &__right {
            display: contents;
        }
    }

    .legal-info__block {
        border-bottom: 1px solid var(--liniya);
        padding-bottom: 20px;

    }

    .legal-info__title {
        font-size: 18px;
        & p {
            margin-bottom: 0 !important;
        }
    }

    .legal-info__supervision-adress {
        font-family: var(--font-family);
        font-weight: 400;
        font-size: 16px;
        line-height: 130%;
        color: #83838f;
    }

    .legal-info__green-block p {
        font-family: var(--font-family);
        font-size: 16px;
        line-height: 130%;
        color: var(--tekst-svetlyy);
        font-weight: 500;
        color: var(--tekst-temnyy);
    }


    .legal-info__green-block a:not(.phone-btn-white) {
        font-family: var(--second-family);
        color: var(--knopka);
        text-decoration: none;
    }
}

@media (max-width: 700px) {
    .legal-info {
        &__block {
            flex-direction: column;
            gap: 0;
            margin-bottom: 20px;
        }

        &__accordion {
            display: block;

            &.accordion--active {
                margin-bottom: 20px;
                & .legal-info__btn-more svg path:last-child {
                    transform: rotateX(90deg);
                }
            }
        }

        &__title {
            display: flex;
            justify-content: space-between;
            gap: 20px;
            margin-bottom: 0;
        }

        &__btn-more {
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 20px;
            border: 1px solid var(--tekst-svetlyy);
            min-width: 28px;
            width: 28px;
            height: 28px;

            & svg path {
                transition: all 0.3s ease;
            }
        }

        &__text {
            &.accordion__content--active {
                margin-bottom: 20px;
            }
        }

        &__supervisions {
            flex-direction: column;
        }

        &__supervision {
            & p:first-child {
                font-size: 14px;
            }
        }

        &__green-block {
            padding: 20px;
        }
    }
}