// services-2
.services {
    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__card {
        border-radius: 20px;
        display: flex;
        flex-direction: column;

        &_top {
            display: block;
            border-radius: 20px;
            padding: 30px 30px 20px 30px;
            background: var(--svetlo-korichnevyy) linear-gradient(180deg, rgba(255, 255, 255, 0.67) 0%, #fff 94.1%);
        }

        &_title {
            margin-bottom: 15px;
            display: flex;
            gap: 8px;
            & p {
                font-family: var(--second-family);
                font-size: 20px;
                line-height: 140%;
                color: var(--tekst-temnyy);
                
            }
            & span {
                font-weight: 300;
                font-size: 12px;
                line-height: 140%;
                color: var(--tekst-svetlyy);
            }
        }

        &_labels ul {
            display: flex;
            flex-wrap: wrap;
            gap: 6px;
            & li {
                border: 1px solid var(--liniya);
                border-radius: 60px;
                padding: 8px 16px;
                font-size: 14px;
                color: var(--tekst-svetlyy);
            }
        }


        &_inner {
            border-radius: 0 0 10px 10px;
            padding: 20px 30px;
            height: 100%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }

        &_main {
            margin-bottom: 25px;
        }

        &_list {
            margin-bottom: 20px;
            & ul {
                display: flex;
                flex-direction: column;
                gap: 15px;
                & li a {
                    color: var(--tekst-temnyy);
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    gap: 14px;
                    transition: all 0.3s ease;
                    &::after {
                        content: "";
                        display: inline-block;
                        width: 8px;
                        height: 8px;
                        border-top: 1px solid var(--knopka);
                        border-right: 1px solid var(--knopka);
                        transform: rotate(45deg);
                    }
                    &:hover {
                        opacity: 0.6;
                    }
                }
            }
        }

        &_more {
            border-radius: 60px;
            padding: 10px 20px;
            background: var(--svetlo-korichnevyy-2);
            font-weight: 500;
            font-size: 14px;
            color: var(--tekst-temnyy);
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 10px;
            &::after {
                content: "";
                display: block;
                border-radius: 10px;
                min-width: 8px;
                width: 8px;
                height: 8px;
                background: var(--knopka);
            }
            &:hover {
                background: var(--svetlo-korichnevyy);
                opacity: 1;
                &::after {
                    transform: none;
                }
            }
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            & button {
                padding: 15px 35px;
            }
        }

        &_price {
            font-weight: 700;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
        }
    }
}


// media services-2
@media (max-width: 1070px) {
    .services {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 767px) {
    .services {
        &__cards {
            order: 2;
            grid-template-columns: repeat(1, 1fr);
            gap: 10px;
        }
        &__card {
            &_top {
                padding: 30px 20px 20px 20px;
            }
            &_title {
                margin-bottom: 12px;
                & p {
                    font-size: 18px;
                }
            }
            &_labels ul {
                & li {
                    padding: 6px 12px;
                    font-size: 12px;
                }
            }
            &_inner {
                padding: 10px 20px 20px 20px;
            }
            &_price {
                font-size: 22px;
            }
        }
    }
}
