// form-7
.form-7 {
    &__row {
        display: flex;
        border: 1px solid var(--liniya);
        border-radius: 30px;
        overflow: hidden;
    }

    &__content {
        flex-basis: 60%;
        padding: 40px;
        display: flex;
        flex-direction: column;
        gap: 20px;
        justify-content: space-between;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 30px;
        line-height: 140%;
        color: var(--tekst-temnyy);
    }

    &__text {
        display: flex;
        gap: 30px;
        align-items: center;
        & p {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
        }
    }

    &__phone-btn {
        background: var(--knopka);
        color: #fff;
    }

    &__inner {
        flex-basis: 45%;
        border-radius: 30px;
        padding: 40px;
        background: var(--knopka);
        & p:first-child {
            margin-bottom: 30px;
            color: #fff;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 10px;

        &_btn {
            background: var(--svetlo-korichnevyy);
            color: var(--knopka);
        }
    }
}

// media form-7
@media (max-width: 1060px) {
    .form-7 {
        &__row {
            flex-direction: column;
        }
        &__phone-btn {
            border: 1px solid var(--liniya);
            background-color: #fff;
            color: var(--knopka);
            & svg path {
                fill: var(--knopka);
                stroke: var(--knopka);
            }
        }
    }
}
@media (max-width: 688px) {
    .form-7 {
        &__content {
            padding: 20px;
            border-radius: 20px;
        }
        &__title {
            font-size: 22px;
        }
        &__text {
            flex-direction: column;
            align-items: flex-start;
            gap: 20px;
        }
        &__inner {
            padding: 20px;
            border-radius: 20px;
            & p:first-child {
                margin-bottom: 20px;
            }
        }
    }
}
