// intro-4

.intro-4 {
    padding: 80px 0;
    position: relative;
    overflow: hidden;
    border-radius: 0 0 20px 20px;

    &__row {
        display: flex;
        position: relative;
        z-index: 2;
    }

    &__advantages {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 50px;
        min-width: 250px;
        padding-right: 50px;
        border-right: 1px solid #F3EEE91A;
        margin-right: 50px;
    }

    &__labels ul {
        border-radius: 20px;
        padding: 20px;
        background: #F3EEE91A;

        display: flex;
        flex-direction: column;
        gap: 14px;

        & li {
            display: flex;
            gap: 14px;
            align-items: center;

            & p,
            span {
                display: block;
                font-size: 14px;
                color: var(--belyy);
            }
        }
    }

    &__cards ul {
        display: flex;
        flex-direction: column;
        gap: 34px;

        & li {
            font-size: 14px;
            line-height: 140%;
            color: var(--belyy);

            & img {
                margin-bottom: 14px;
            }
        }
    }

    &__labels-mobile,
    &__cards-mobile {
        display: none;
    }

    &__main {
        color: var(--belyy);

        &_top {
            display: flex;
            gap: 20px;
            margin-bottom: 40px;
        }

        & h1, h2 {
            color: var(--belyy);
        }

        &_subtitle {
            font-family: var(--font-family);
            font-weight: 300;
            font-size: 16px;
            line-height: 140%;
            color: #fff;
        }

        &_info {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
            gap: 28px;
            flex-basis: 100%;
        }

        &_bottom {
            display: flex;
            gap: 47px;
        }
    }

    &__form {
        border-radius: 20px;
        padding: 30px 40px;
        backdrop-filter: blur(25px);
        background: rgba(243, 238, 233, 0.1);
        color: var(--belyy);
        max-width: 505px;

        &_title {
            font-weight: 700;
            font-size: 22px;
            margin-bottom: 8px;
        }

        &_phone {
            color: var(--belyy);
            margin-bottom: 10px;
        }

        &_subtitle {
            margin-bottom: 20px;
        }

        &_inner {
            display: flex;
            flex-direction: column;
            gap: 10px;
        }

        &_input .input {
            width: 100%;
            background: rgba(255, 255, 255, 0.15);
        }
    }


    &__stocks {
        padding: 20px 40px;
        border-right: 1px solid #F3EEE91A;
        border-left: 1px solid #F3EEE91A;
        max-width: 467px;
        display: flex;
        flex-direction: column;
        gap: 30px;
    }

    &__stock {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 28px;

        & p {
            font-size: 16px;
            line-height: 140%;
            margin-bottom: 10px;
        }

        & span {
            font-weight: 700;
            font-size: 40px;
            line-height: 120%;
            color: var(--belyy);
        }

        &_btn {
            color: var(--belyy);
            padding: 10px 0;
            &::after {
                border-top: 1px solid var(--belyy);
                border-right: 1px solid var(--belyy);
            }
        }

        &_bottom {
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            gap: 20px;

            & img {
                border-radius: 160px;
                width: 180px;
                height: 180px;
            }
        }
    }

    &__paginations {
        display: flex;
        gap: 30px;
        align-items: center;
        justify-content: space-between;
    }

    .intro-4__paginations .intro__swiper-pagination {
        text-wrap: nowrap;
        flex-basis: 32px;
    }
}


// media intro-4
@media (max-width: 1173px) {
    .intro-4 {
        padding: 60px 0 0;
        border-radius: 0;
        &__advantages {
            display: none;
        }

        &__labels-mobile,
        &__cards-mobile {
            display: block;
        }
        &__labels-mobile {
            background-color: white;
            color: #37384C;
            padding: 40px 20px 20px;
            margin-left: -20px;
            margin-right: -20px;
        }

        &__labels-mobile ul {
            border: 1px solid var(--liniya);
            border-radius: 15px;
            padding: 16px 0;
            display: flex;
            justify-content: center;
         
            & li:last-child {
                border-left: 1px solid #625B5A33;
            }

            & li {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 0px 21px;

                & p, span {
                    display: block;
                    font-size: 12px;
                    color: #37384C;
                }
                & p {
                    margin-left: 12px;
                }
           
            }
        }

        &__cards-mobile ul {
            display: flex;
            flex-wrap: wrap;
            gap: 20px 10px;
            margin-bottom: 10px;

            & li {
                flex: 1 0 40%;
                font-size: 14px;
                color: var(--belyy);

                & img {
                    margin-bottom: 15px;
                }
            }
        }

        &__main {
            max-width: 100%;

            &_top {
                flex-direction: column;
                gap: 20px;
                margin-bottom: 30px;
            }

            &_info {
                gap: 20px;
            }

            &_bottom {
                flex-direction: column-reverse;
                gap: 0;
            }
        }

        &__form-wrap {
            background-color: white;
            padding: 0 20px;
            margin: -1px -20px;
        }
        
        &__form {
            max-width: 100%;
            border-radius: 15px;
            padding: 40px;
            background: var(--svetlo-korichnevyy-2);
            color: var(--tekst-svetlyy);
            
            &_title {
                color: var(--tekst-temnyy);
            }

            &_phone {
                color: var(--knopka);
                margin-bottom: 12px;
                & svg path {
                    fill: var(--knopka);
                    stroke: var(--knopka);
                }
            }

            &_input .input {
                background: rgba(255, 255, 255, 0.8);
                color: var(--tekst-svetlyy);
                &:hover, &:focus {
                    background: rgba(255, 255, 255, 1);
                }
                &::placeholder {
                    color: var(--tekst-svetlyy);
                }
            }

            &_politic {
                color: var(--tekst-svetlyy);
            }

            &_btn {
                color: var(--belyy);
                background: var(--knopka);
            }
        }

        &__stocks {
            flex-direction: column-reverse;
            gap: 20px;
            padding: 20px 0;
            border-right: none;
            border-left: none;
            border-top: 1px solid rgba(255, 255, 255, 0.4);
            border-bottom: 1px solid rgba(255, 255, 255, 0.4);
            max-width: 100%;
            margin-bottom: 40px;
        }

        &__stock {
            position: relative;
            gap: 70px;

            &_bottom {
                & img {
                    position: absolute;
                    bottom: 0;
                    right: 0;
                    width: 160px;
                    height: 160px;
                }
            }
        }
    }
}

@media (max-width: 688px) {
    .intro-4 {
        padding-top: 40px;
        &__main_btn {
            width: 100%;
        }
        &__form {
            padding: 20px;
            &_title {
                font-size: 18px;
            }
            &_phone {
                font-size: 18px;
            }
        }
        &__stock {
            position: relative;
            gap: 50px;
            & span {
                font-size: 32px;
            }
            &_btn {
                padding: 0;
            }
            &_bottom {
                & img {
                    width: 112px;
                    height: 112px;
                }
            }
        }
        &__paginations {
            gap: 20px;
        }
    }

}