.popup-change {
    &__content {
        max-width: 948px;
        border-radius: 20px;
        padding: 60px;
        box-shadow: 0 0 30px 0 rgba(255, 255, 255, 0.1);
        background: var(--fon-plashek-bezhevyy);
        display: flex;
    }

    &__left {
        padding-right: 40px;
        border-right: 1px solid var(--liniya);
        margin-right: 40px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
        flex-basis: 60%;
    }

    &__title {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 34px;
        line-height: 140%;
        color: var(--tekst-temnyy);
        margin-bottom: 8px;
    }

    &__subtitle {
        color: var(--tekst-svetlyy);
    }

    &__phone {
        & p {
            font-size: 14px;
            color: var(--tekst-svetlyy);
            margin-bottom: 6px;
        }

        & a {
            font-weight: 700;
            font-size: 22px;
            color: var(--knopka);
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__input:hover {
        background-color: var(--belyy);
    }

    &__close {
        width: 40px;
        height: 40px;
        top: 0;
        right: -50px;
    }
}




.popup-change-response {
    &__content {
        max-width: 606px;
        padding: 10px;
        display: flex;
        flex-direction: column;
    }
    &__wrapper {
        & h3 {
            border-radius: 20px;
            padding: 30px 10px;
            background: var(--belyy);
            box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.06);
            text-align: center;
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 30px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 30px;
        }

        & p {
            max-width: 58%;
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            gap: 20px;
            padding: 0 30px 30px 30px;
            text-align: center;
            color: var(--tekst-svetlyy);
            &::before {
                content: url("./../img/popups/popup-call-1-ok.svg");
            }
        }
    }
    &__btn {
        border-top: 1px solid var(--liniya);
        padding-top: 30px;
        margin: 0 30px 30px;
        & a {
            display: block;
            width: 100%;
        }
    }
}

.popup[data-target="popup-change-ok"] {
    & p::before {
        content: url("./../img/popups/popup-call-1-ok.svg");
    }
}
.popup[data-target="popup-change-error"] {
    & p::before {
        content: url("./../img/popups/popup-call-1-error.svg");
    }
    & .popup-change-response__btn {
        display: none;
    }
}



// media popup-change-1
@media (max-width: 1050px) {
    .popup-change {
        &__content {
            border-radius: 20px;
            padding: 20px;
            flex-direction: column;
            max-width: 480px;
        }
        &__left {
            padding-right: 0;
            border-right: none;
            margin-right: 0;
            gap: 20px;
            flex-basis: 100%;
            align-items: flex-start;
            padding-bottom: 24px;
            border-bottom: 1px solid var(--liniya);
            margin-bottom: 24px;
        }
        &__title {
            font-size: 18px;
        }
        &__subtitle {
            font-size: 14px;
        }
        &__phone {
            & a {
                font-size: 18px;
            }
            & svg {
                width: 20px;
                height: 20px;
            }
        }
    }

    .popup-change-response {
        &__content {
            max-width: 400px;
            padding: 10px;
            display: flex;
            flex-direction: column;
        }
        &__wrapper {
            & h3 {
                padding: 16px 10px;
                font-size: 18px;
            }
            & p {
                max-width: 80%;
                gap: 14px;
                padding: 0 10px 20px;
            }
        }
        &__btn {
            padding-top: 20px;
            margin: 0 20px 20px;
        }
    }
}
@media (max-width: 600px) {
    .popup-change {
        &__close {
            width: 30px;
            height: 30px;
            top: -40px;
            right: 0;
        }
    }
}