// page-articles-2
.page-articles {
    &__sort {
        display: flex;
        justify-content: space-between;
        gap: 10px;
        margin-bottom: 50px;
        &_search {
            flex-basis: 25%;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px 24px;
    }

    &__card {
        background: var(--fon-plashek-bezhevyy);
        overflow: hidden;
        transition: all 0.5s ease;

        &_date {
            background: #fff;
        }
    }

    &__btn-arrow {
        display: none;
    }
}


// media page-articles-2
@media (max-width: 1100px) {
    .page-articles {
        &__sort {
            flex-direction: column;
            gap: 25px;
            margin-bottom: 30px;
        }
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            gap: 30px 24px;
        }
    }
}
@media (max-width: 688px) {
    .page-articles {
        &__sort {
            gap: 10px;
        }
        &__btn-arrow {
            display: block;
        }
        &__tab {
            &_btns {
                display: flex;
                flex-direction: column;
                gap: 0;
                max-height: 51px;
                overflow: hidden;
                margin-bottom: 0;
                transition: all 0.3s ease;
            }
            &_btns.acc-active {
                max-height: 400px;
                .page-articles__btn-arrow {
                    transform: rotate(180deg);
                }
            }
            &_btn {
                border-radius: 0;
                padding: 16px 0;
                background: transparent;
                text-align: left;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0 20px;
                .page-articles__btn-arrow {
                    opacity: 0;
                }
                &.active {
                    margin: 0;
                    order: -1;
                    border-radius: 30px;
                    padding: 16px 20px;
                    border: 1px solid var(--liniya);
                    .page-articles__btn-arrow {
                        opacity: 1;
                    }
                }
            }
        }
        &__btn-arrow {
            display: flex;
            margin: 0;
            &::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 4px;
            }
        }
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 12px;
        }
    }
}


