
// page-reviews-3
.page-reviews {
    border-radius: 0 0 20px 20px;
    padding: 70px 0 140px;
    background: var(--fon-plashek-bezhevyy);

    &__sort {
        display: flex;
        gap: 12px;
        margin-bottom: 24px;
        & .sort_block {
            max-width: 436px;
            width: 100%;
        }
    }

    &__cards {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }
}


// media page-reviews-3
@media (max-width: 950px) {
    .page-reviews {
        &__cards {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 700px) {
    .page-reviews {
        padding: 40px 0 60px;
        &__sort {
            flex-direction: column;
            gap: 10px;
            margin-bottom: 30px;
            & .sort_block {
                max-width: 100%;
            }
        }
    }
}
