// doctors-3
.doctors {
    overflow: hidden;

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        margin-bottom: 30px;
    }

    &__card {
        display: flex;
        gap: 10px;
        height: 100%;
        position: relative;
        overflow: hidden;

        &_inner {
            border-radius: 20px;
            padding: 28px;
            background: var(--fon-plashek-bezhevyy);
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 30px;
            flex: 1 0 60%;
        }

        &_experience {
            border-radius: 60px;
            padding: 10px 16px;
            background: #fff;
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            color: var(--tekst-svetlyy);
            width: fit-content;
            margin-bottom: 12px;
        }

        &_fio {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 22px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 12px;
        }

        &_item {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            &:first-child {
                padding-bottom: 8px;
                border-bottom: 1px solid rgba(98, 91, 90, 0.2);
                margin-bottom: 8px;
            }
        }

        &_btns {
            display: flex;
            gap: 6px;
        }

        &_photo {
            flex: 1 0 38%;
            display: block;
            border-radius: 20px;
            background: var(--fon-plashek-bezhevyy);
            overflow: hidden;
            max-height: 350px;
            & img {
                display: block;
                width: 100%;
                height: 100%;
                object-fit: cover;
                object-position: top;
            }
        }
    }

}


// media doctors-3
@media (max-width: 800px) {
    .doctors {
        &__swiper-btns {
            justify-content: center;
        }
    }
}
@media (max-width: 600px) {
    .doctors {
        &__card {
            flex-direction: column;
            height: auto;
            &_inner {
                border-radius: 20px;
                padding: 20px;
                gap: 20px;
                max-width: 100%;
                height: auto;
                position: relative;
            }
            &_experience {
                position: absolute;
                background: rgba(255, 255, 255, 0.8);
                top: -60px;
                left: 10px;
                right: auto;
            }
            &_fio {
                font-size: 18px;
            }
            &_btns {
                & a, & button {
                    flex: 1 0 40%;
                }
            }
            &_photo {
                border-radius: 20px;
                max-height: 280px;
            }
        }
    }
}
@media (max-width: 460px) {
    .doctors {
        &__card {
            &_photo {
                max-height: 240px;
            }
            &_btns {
                flex-direction: column;
            }
        }
    }
}

