/* linking */

.linking__title {
    margin-bottom: 50px;
}

.linking__block:not(:last-child) {
    margin-bottom: 70px;
}

.linking__inner {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;

    &>* {
        flex-basis: calc(50% - 5px);
    }
}

.linking__item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: var(--fon-plashek-bezhevyy);
    padding: 20px;
    border-radius: 60px;
}

.linking__item-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-basis: 30%;
}

.linking__item-title {
    font-family: var(--second-family);
    font-size: 18px;
    color: var(--tekst-temnyy);
    flex-basis: 70%;
    border-right: 1px solid var(--liniya);
    margin-right: 30px;
    padding-right: 20px;
}

.linking__btn {
    max-width: 30px;
    max-height: 30px;
    min-width: 30px;
    min-height: 30px;
}

/* linking media */

@media (max-width: 1024px) {
    .linking__item {
        font-size: 14px;
    }
}

@media (max-width: 930px) {
    .linking__title {
        margin-bottom: 20px;
    }
    
    .linking__item {
        align-items: flex-start;
        flex-direction: column;
        height: auto;
        border-radius: 20px;
    }

    .linking__item-title {
        font-size: 14px;
        flex-basis: 100%;
        border-right: none;
        margin-right: 0;
        padding-right: 0;
        flex-grow: 1;
    }
   
    .linking__item-bottom {
        border-top: 1px solid var(--liniya);
        margin-top: 14px;
        padding-top: 8px;
        flex-basis: 100%;
        width: 100%;
    }
}

@media (max-width: 550px) {
    .linking__item {
        flex-basis: 100%;
    }
}