// gallery-3
.gallery {
    &__inner {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: flex-end;
        gap: 50px 30px;
        margin-bottom: 30px;
    }

    &__tab {
        &_btns {
            display: flex;
            width: fit-content;
        }
    }

    &__btn-arrow {
        display: none;
    }

    &__cards {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
    }

    &__image {
        display: block;
        border-radius: 20px;
        overflow: hidden;
        position: relative;
        width: 100%;
        height: 100%;
        max-height: 280px;

        & img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    &__label {
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        z-index: 5;
        border-radius: 20px;
        padding: 10px 16px;
        backdrop-filter: blur(7.400000095367432px);
        background: rgba(98, 91, 90, 0.4);
        font-weight: 600;
        font-size: 14px;
        line-height: 140%;
        color: #fff;
    }

    &__btn {
        display: block;
        margin: 0 auto;
        width: fit-content;
    }
}

.gallery-page {
    &__cards {
        margin-top: 50px;
    }
}

.gallery-about {
    padding: 70px 0;
    background: var(--fon-plashek-seryy);
    &__cards {
        width: 100%;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        gap: 20px;
        & .gallery__image:first-child {
            grid-row: span 2;
            grid-column: span 2;
            max-height: none;
        }
    }
}

// media gallery-3
@media (max-width: 1150px) {
    .gallery {
        &__inner {
            flex-direction: column;
            gap: 30px;
            align-items: stretch;
        }
        &__tab {
            &_btns {
                width: 100%;
            }
            &_btn {
                flex: 1 0 20%;
            }
        }
        &__cards {
            grid-template-columns: repeat(3, 1fr);
            gap: 20px;
        }
    }
}
@media (max-width: 940px) {
    .gallery {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            grid-auto-rows: 1fr;
            gap: 20px;
        }
    }

    .gallery-about {
        &__cards {
            grid-template-columns: repeat(2, 1fr);
            & .gallery__image:first-child {
                max-height: 320px;
            }
            & .gallery__image {
                min-width: auto;
            }
        }
    }
    
}
@media (max-width: 600px) {
    .gallery {
        &__inner {
            gap: 23px;
            margin-bottom: 23px;
        }
        &__cards {
            grid-template-rows: minmax(240px, 280px);
            grid-auto-flow: column;
            overflow-x: auto; 
            gap: 10px;
            scroll-snap-type: x mandatory;
            &::-webkit-scrollbar {
                height: 0;
            }
        }
        &__image {
            min-width: calc(100vw - 40px);
            height: 100%;
            border-radius: 20px;
            scroll-snap-align: start;
        }
        &__btn {
            width: 100%;
        }
        &__label {
            border-radius: 20px;
            padding: 5px 10px;
            font-size: 12px;
        }

        &__tab {
            &_btns {
                display: flex;
                flex-direction: column;
                gap: 0;
                max-height: 51px;
                overflow: hidden;
                margin-bottom: 0;
                transition: all 0.3s ease;
            }
            &_btns.acc-active {
                max-height: 400px;
                .gallery__btn-arrow {
                    transform: rotate(180deg);
                }
            }
            &_btn {
                border-radius: 0;
                padding: 16px 0;
                background: transparent;
                text-align: left;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0 20px;
                .gallery__btn-arrow {
                    opacity: 0;
                }
                &.active {
                    margin: 0;
                    order: -1;
                    border-radius: 30px;
                    padding: 16px 20px;
                    border: 1px solid var(--liniya);
                    .gallery__btn-arrow {
                        opacity: 1;
                    }
                }
            }
        }
        &__btn-arrow {
            display: flex;
            margin: 0;
            &::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 4px;
            }
        }
    }

    .gallery-page {
        &__cards {
            margin-top: 30px;
            grid-template-columns: repeat(1, 1fr);
            grid-template-rows: auto;
            grid-auto-flow: row;
            overflow-x: hidden; 
            gap: 12px;
            padding-bottom: 0;
        }
    }

    .gallery-about {
        padding: 40px 0;
        &__cards {
            gap: 10px;
            & .gallery__image {
                min-width: auto;
                border-radius: 16px;
            }
        }
    }
}