// faq-1
.faq {
    padding: 140px 0;
    position: relative;
    overflow: hidden;
    border-radius: 20px 20px 0 0;

    &__title {
        color: #fff;
    }

    &__btn {
        border: 1px solid var(--fon-plashek-seryy);
        color: var(--fon-plashek-seryy);
        background-color: transparent;
    }

    &__grid {
        display: grid;
        grid-template-columns: 6.2fr 3.2fr;
        gap: 60px;
    }

    &__list li {
        margin-bottom: 12px;
    }

    &__button {
        border-radius: 20px;
        padding: 24px 30px;
        backdrop-filter: blur(20px);
        background: rgba(65, 66, 86, 0.6);
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        gap: 0 20px;
        text-align: left;
        line-height: 140%;
    }
    &__button.accordion--active {
        gap: 20px;
        & .faq__btn-more {
            & svg path:last-child {
                transform: rotateX(90deg);
            }
        }
    }

    &__name {
        font-family: var(--second-family);
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        color: var(--fon-plashek-seryy);
        flex-basis: 80%;
    }
    
    &__content {
        flex-basis: 100%;
        max-height: 0;
        overflow: hidden;
        color: var(--fon-plashek-seryy);
        transition: all 0.3s ease;
        box-sizing: border-box;
        &_text {
            opacity: 0.8;
        }
    }

    &__btn-more {
        display: block;
        width: 34px;
        height: 34px;
        border: 1px solid var(--fon-plashek-seryy);
        border-radius: 20px;
        & svg path {
            transition: all 0.3s ease;
        }
    }

    &__editor {
        border-radius: 20px;
        padding: 20px;
        background: rgba(243, 244, 246, 0.1);
        display: flex;
        gap: 20px;
        margin-top: 30px;

        &_left {
            display: flex;
            gap: 16px;
            align-items: center;
            padding-right: 20px;
            border-right: 1px solid rgba(243, 244, 246, 0.2);
            flex-basis: 48%;
        }

        &_img {
            display: block;
            border-radius: 100px;
            min-width: 68px;
            width: 68px;
            height: 68px;
            overflow: hidden;
            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_text {
            font-size: 14px;
            line-height: 150%;
            color: var(--fon-plashek-seryy);
            opacity: 0.6;
        }

        &_fio {
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            color: var(--fon-plashek-seryy);
        }

        &_right {
            display: flex;
            flex-direction: column;
            justify-content: center;
            gap: 4px;
        }

        &_item {
            display: flex;
            align-items: flex-start;
            gap: 10px;
            font-size: 14px;
            line-height: 150%;
            color: var(--fon-plashek-seryy);
        }
    }


    &__form {
        border-radius: 20px;
        padding: 40px;
        background: var(--seryy);

        &_title {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 12px;
        }

        &_subtitle {
            color: var(--tekst-svetlyy);
            margin-bottom: 30px;
        }

        &_inputs {
            display: flex;
            flex-direction: column;
            gap: 10px;   
        }

        &_btn {
            background: var(--tekst-temnyy);
        }
    }
}

.faq-service {
    padding: 0;
    color: var(--tekst-temnyy);
    & .faq__title {
        color: var(--tekst-temnyy);
    }

    & .faq__btn {
        border: 1px solid var(--tekst-temnyy);
        color: var(--tekst-temnyy);
    }

    & .faq__button {
        backdrop-filter: blur(20px);
        background: var(--fon-plashek-bezhevyy);
    }

    & .faq__name {
        color: var(--tekst-temnyy);
    }
    
    & .faq__content {
        color: var(--tekst-temnyy);
    }

    & .faq__btn-more {
        border: 1px solid var(--tekst-temnyy);
    }

    & .faq__editor {
        background: var(--svetlo-korichnevyy-2);
        &_text {
            color: var(--tekst-svetlyy);
        }
        &_fio {
            color: var(--tekst-temnyy);
        }
        &_item {
            color: var(--tekst-temnyy);
        }
    }


    & .faq__form {
        background: var(--tekst-temnyy);

        &_title {
            color: #fff;
        }

        &_subtitle {
            color: #fff;
            opacity: 0.8;
        }

        &_btn {
            background: var(--svetlo-korichnevyy-2);
            color: var(--tekst-temnyy);
        }
    }
}


// media faq-1
@media (max-width: 1150px) {
    .faq {
        padding: 100px 0;
        &__grid {
            display: grid;
            grid-template-columns: 1fr;
            gap: 30px;
            display: contents;
        }
        &__block_form {
            order: 5;
        }
    }
}
@media (max-width: 688px) {
    .faq {
        padding: 60px 0;
        &__button {
            border-radius: 20px;
            padding: 20px;
        }
        &__name {
            font-size: 16px;
        }
        &__btn-more {
            width: 28px;
            height: 28px;
        }
        &__editor {
            flex-direction: column;
            margin-top: 20px;
            gap: 16px;
            &_left {
                padding-right: 0;
                border-right: none;
                margin-right: 0;
                padding-bottom: 16px;
                border-bottom: 1px solid rgba(255, 255, 255, 0.2);
            }
            &_img {
                min-width: 56px;
                width: 56px;
                height: 56px;
            }
        }
        &__form {
            border-radius: 20px;
            padding: 20px;
            &_title {
                font-size: 16px;
            }
            &_subtitle {
                margin-bottom: 16px;
            }
        }
        &__btn {
            margin-bottom: 10px;
        }
    }
}





// page-faq-2
.page-faq {
    &__sort {
        margin-bottom: 50px;
    }
    &__btn-arrow {
        display: none;
    }
}

// media page-faq-2
@media (max-width: 1100px) {
    .page-faq {
        &__sort {
            margin-bottom: 30px;
        }
    }
}
@media (max-width: 750px) {
    .page-faq {
        &__btn-arrow {
            display: block;
        }
        &__tab {
            &_btns {
                display: flex;
                flex-direction: column;
                gap: 0;
                max-height: 51px;
                overflow: hidden;
                margin-bottom: 0;
                transition: all 0.3s ease;
            }
            &_btns.acc-active {
                max-height: 400px;
                .page-faq__btn-arrow {
                    transform: rotate(180deg);
                }
            }
            &_btn {
                border-radius: 0;
                padding: 16px 0;
                background: transparent;
                text-align: left;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0 20px;
                .page-faq__btn-arrow {
                    opacity: 0;
                }
                &.active {
                    margin: 0;
                    order: -1;
                    border-radius: 30px;
                    padding: 16px 20px;
                    border: 1px solid var(--liniya);
                    .page-faq__btn-arrow {
                        opacity: 1;
                    }
                }
            }
        }
        &__btn-arrow {
            display: flex;
            margin: 0;
            &::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 4px;
            }
        }
    }
}
