// reviews-4
.reviews {
    padding: 140px 0;
    border-radius: 0 0 20px 20px;
    background: var(--fon-plashek-bezhevyy);
    overflow: hidden;

    &__swiper-slide {
        height: auto;
    }

    &__cards {
        margin-bottom: 30px;
    }

    &__card {
        border-radius: 30px;
        background: var(--svetlo-korichnevyy-2);
        overflow: hidden;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &_inner {
            border-radius: 30px;
            padding: 40px;
            background: #fff;
            height: 100%;
        }

        &_info {
            display: flex;
            justify-content: space-between;
            align-items: center;
            gap: 20px;
            padding-bottom: 18px;
            border-bottom: 1px solid var(--liniya);
            margin-bottom: 18px;
        }

        &_fio {
            display: flex;
            flex-direction: column;
            gap: 4px;
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            & p:first-child {
                font-family: var(--second-family);
                font-size: 22px;
                color: var(--tekst-temnyy);
            }
        }

        &_right {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            gap: 6px;
        }

        &_publication{
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            &::before {
                display: inline-block;
                content: url("./../img/icons/calendar-2.svg");
                width: 18px;
                height: 20px;
                margin-right: 8px;
                vertical-align: middle;
            }
        }

        &_stars {
            display: flex;
            gap: 4px;
        }
        &_star-active {
            & svg path {
                fill: #ffda33;;
            }
        }

        &_more::after {
            transform: rotate(135deg);
            margin-bottom: 2px;
        }

        &_text {
            margin-bottom: 30px;
            max-height: 154px;
            overflow: hidden;
        }

        &_answer {
            padding: 30px;
        }

        &_answered {
            padding-top: 14px;
            border-top: 1px solid var(--liniya);
            margin-top: 14px;
            display: flex;
            justify-content: space-between;
            color: var(--tekst-temnyy);
            & p:first-child {
                font-weight: 500;
                font-size: 14px;
            }
            & p:last-child {
                font-family: var(--second-family);
                font-size: 18px;
                text-align: right;
                color: var(--tekst-temnyy);
            }
        }
    }
}



// media reviews-4
@media (max-width: 800px) {
    .reviews {
        padding: 100px 0;
        &__cards {
            margin-bottom: 20px;
        }
        &__swiper-btns {
            justify-content: center;
        }
    }
}
@media (max-width: 700px) {
    .reviews {
        padding: 60px 0;
        &__card {
            border-radius: 20px;
            &_inner {
                border-radius: 20px;
                padding: 20px;
            }
            &_info {
                flex-direction: column-reverse;
                align-items: stretch;
                gap: 14px;
            }
            &_fio {
                & p:first-child {
                    font-size: 18px;
                }
            }
            &_right {
                flex-direction: row-reverse;
                justify-content: space-between;
                align-items: center;
                gap: 16px;
            }
            &_text {
                max-height: 240px;
                margin-bottom: 20px;
            }
            &_answer {
                padding: 20px;
            }
    
            &_answered {
                flex-direction: column;
                & p:last-child {
                    font-size: 16px;
                    text-align: left;
                }
            }
        }
    }
}