/* Navigation */
.navigation__inner-4 {
    width: 100%;
    height: 100%;
    padding: 60px;
    border-radius: 20px;
    display: flex;
    justify-content: space-between;
    position: relative;
    z-index: 1;
    margin-bottom: 20px;
    background: var(--fon-plashek-seryy);
    gap: 20px;

}

.dock-check__inner-4 {
    display: flex;
    flex-direction: column;
    gap: 20px;
    flex-basis: calc(50% - 10px);

    &>* {
        flex-basis: calc(50% - 10px);
    }
}

.doc-check-4 {
    border-radius: 20px;
    padding: 30px;
    border: 1px solid transparent;
    backdrop-filter: blur(20px);
    background: rgba(255, 255, 255, 0.5);
    transition: border-color 0.3s ease;
    color: var(--tekst-svetlyy);

    &:hover {
        border-color: var(--knopka);
    }
}

.doc-check__name-4 {
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 140%;
    color: var(--tekst-temnyy);
}

.doc-check__top-4 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    font-size: 14px;
    line-height: 140%;
    color: var(--tekst-svetlyy);
    opacity: 0.8;
    padding-bottom: 10px;
    margin-bottom: 16px;
    border-bottom: 1px solid rgba(55, 56, 76, 0.1);
}

.doc-check__content-4 {
    display: flex;
    gap: 20px;
}

.doc-check__picture-4 {
    max-width: 120px;
    max-height: 120px;
    border-radius: 20px;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background: radial-gradient(circle, rgba(255,255,255,1) 0%, rgba(229,241,239,1) 100%);

    & img {
        object-fit: cover;
        border-radius: 20px;
        width: 100%;
        height: 100%;
        object-position: top;
    }
}

.doc-check__info-4 {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    gap: 10px;
}

.navigation__wrap-4 {
    flex-basis: calc(50% - 10px);
}

.navigation__title-4 {
    margin-bottom: 30px;
}

.navigation__list-4 {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.navigation__link-4 {
    position: relative;
    transition: all 0.3s ease;
    font-weight: 600;

    &:hover {
        text-decoration: none;
    }
}

.navigation__item-4 {
    font-size: 16px;
    font-weight: 400;
    line-height: 1.56;
}

.navigation__list-4 .nav-title-h3 .navigation__link,
.navigation__list-4 .nav-title-h4 .navigation__link,
.navigation__list-4 .nav-title-h5 .navigation__link {
    display: flex;
    gap: 10px;
    padding-left: 40px;
    color: var(--tekst-svetlyy);
    font-weight: 400;
    transition: color 0.3s ease;
    position: relative;


    &:after {
        content: '';
        position: absolute;
        left: 20px;
        top: 8px;
        width: 12px;
        height: 3px;
        border-radius: 10px;
        background: var(--seryy);
    }

    &:hover {
        color: var(--korichnevyy);
    }
}

.navigation__list-4 .nav-title-h2 .navigation__link {
    display: flex;
    gap: 10px;
    padding-left: 20px;
    color: var(--tekst-temnyy);
    font-weight: 700;
    position: relative;
    transition: color 0.3s ease;

    &:after {
        content: '';
        position: absolute;
        left: 0;
        top: 8px;
        width: 15px;
        height: 3px;
        border-radius: 10px;
        background: var(--seryy);
    }

    &:hover {
        color: var(--korichnevyy);
    }
}

.doc-check__post-4 {
    position: relative;
    padding-left: 20px;

    &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 14px;
        height: 16px;
        background: url("./../img/icons/doctor.svg") no-repeat center center / cover;
    }
}


.doc-check__stage-4 {
    position: relative;
    padding-left: 20px;

    &:after {
        content: '';
        position: absolute;
        top: 3px;
        left: 0;
        width: 16px;
        height: 16px;
        background: url("./../img/icons/calendar.svg") no-repeat center center / cover;
    }
}

/* Navigation media */


@media (max-width: 1200px) {
    .navigation__inner-4 {
        padding: 20px;
    }

    .doc-check-4 {
        padding: 20px;
        border-radius: 20px;
    }

    .doc-check__content-4 {
        gap: 10px;
    }
}

@media (max-width: 1024px) {
    .navigation__inner-4 {
        flex-direction: column;
        gap: 20px;
    }
}

@media (max-width: 450px) {
    .doc-check__picture-4,
    .doc-check__picture-4 img {
        max-width: 50px;
        max-height: 50px;
        border-radius: 10px;
    }

    .doc-check__name-4 {
        font-size: 16px;
        line-height: 1.1;
    }

    .doc-check__post-4 {
        padding-left: 15px;

        &:after {
            top: 4px;
            width: 11px;
            height: 12px;
        }
    }
    
    .doc-check__stage-4 {
        padding-left: 15px;
    
        &:after {
            top: 4px;
            width: 11px;
            height: 11px;
        }
    }
}

@media (max-width: 400px) {
    .doc-check__info-4 {
        padding-top: 10px;
    }
    .doc-check__post-4,
    .doc-check__stage-4 {
        margin-left: - 55px
    }

}