// contacts-6
.contacts {
    padding: 140px 0;
    background: var(--fon-plashek-bezhevyy);

    &__info {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        margin-bottom: 34px;

        &_line {
            width: 1px;
            min-height: 100%;
            background: var(--liniya);
        }

        &_text {
            font-size: 14px;
            line-height: 140%;
            color: var(--tekst-svetlyy);
            margin-bottom: 8px;
            & a:hover {
                text-decoration: underline;
            }
        }

        &_link {
            font-weight: 500;
            font-size: 16px;
            line-height: 140%;
            color: var(--knopka);
            &-phone {
                font-weight: 700;
                font-size: 22px;
                &:hover {
                    text-decoration: underline;
                }
            }
            &-location {
                font-weight: 400;
                & button {
                    display: block;
                    font-weight: 500;
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }

    &__plashka {
        border-radius: 20px;
        padding: 30px 40px;
        background: #fff;
        color: var(--tekst-svetlyy);
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
        margin-top: 15px;

        &_left {
            display: flex;
            flex-direction: column;
            gap: 4px;
            font-size: 14px;
            line-height: 143%;
            color: var(--tekst-svetlyy);
        }

        &_right {
            display: flex;
            align-items: center;
            gap: 20px;
        }
        
        &_networks {
            display: flex;
            gap: 10px;
            padding-right: 20px;
            border-right: 1px solid rgba(98, 91, 90, 0.2);
        }

        &_network {
            width: 40px;
            height: 40px;
            border-radius: 100px;
            background-color: var(--fon-plashek-bezhevyy);
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease;
            &:hover {
                background: var(--svetlo-korichnevyy);
            }
        }

        &_btns {
            display: flex;
            gap: 10px;
        }
    }

    &__maps {
        border-radius: 20px;
        width: 100%;
        height: 100%;
        min-height: 734px;
        position: relative;
        overflow: hidden;
        flex-basis: 60%;
    }

    &__map {
        position: absolute;
        inset: 0;
        z-index: 2;
        width: 100%;
        height: 100%;
    }
}

.contacts-page {
    & .contacts__plashka {
        background: var(--fon-plashek-bezhevyy);
    }
}


// media contacts
@media (max-width: 1120px) {
    .contacts {
        padding: 100px 0;
        &__info {
            flex-direction: column;
            gap: 20px;
            &_line {
                min-width: 100%;
                min-height: 1px;
                height: 1px;
                &:first-child, &:last-child {
                    display: none;
                }
            }
            &_link {
                &-location {
                    & button {
                        display: inline-block;
                    }
                }
            }
        }
        &__plashka {
            flex-direction: column;
            align-items: stretch;
            gap: 20px;
            margin-top: 40px;
            &_left {
                flex-direction: column;
                align-items: stretch;
            }
            &_right {
                flex-direction: column-reverse;
                align-items: stretch;
                gap: 20px;
            }
            &_networks {
                display: flex;
                gap: 10px;
                padding-right: 0;
                border-right: none;
            }
        }
    }
}
@media (max-width: 900px) {
    .contacts {
        &__maps {
            min-height: 450px;
        }
    }
}
@media (max-width: 700px) {
    .contacts {
        padding: 60px 0;
        &__info {
            &_link {
                font-size: 14px;
                &-phone {
                    font-size: 18px;
                }
            }
        }
        &__plashka {
            border-radius: 20px;
            padding: 20px;
            &_btns {
                flex-direction: column;
            }
        }
        &__maps {
            min-height: 340px;
        }
    }
}