// stocks-page-5
.stocks-page {
    &__sort {
        margin-bottom: 50px;
    }

    &__btn-arrow {
        display: none;
    }

    &__cards {
        display: flex;
        flex-direction: column;
        gap: 20px;
    }

    &__card {
        padding: 0px;

        display: flex;
        gap: 60px;
        justify-content: space-between;

        border: 1px solid var(--liniya);
        border-radius: 20px;

        &_main {
            flex: 1 0 50%;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            gap: 36px;
            padding: 30px;
        }

        &_name {
            font-family: var(--second-family);
            font-weight: 400;
            font-size: 26px;
            line-height: 140%;
            color: var(--tekst-temnyy);
            margin-bottom: 20px;
        }

        &_btn {
            width: fit-content;
        }

        &_img {
            flex: 1 0 33%;
            display: block;
            border-radius: 20px;
            overflow: hidden;
            position: relative;
            width: 100%;
            min-height: 100%;

            & img {
                width: 100%;
                height: 100%;
                object-fit: cover;

            }
        }

        &_date {
            position: absolute;
            top: 16px;
            left: 16px;
            background: var(--liniya);
            width: fit-content;
            border-radius: 30px;
            padding: 8.5px 18.5px;
            font-family: var(--font-family);
            font-weight: 600;
            font-size: 14px;
            line-height: 140%;
            color: #fff;
        }
    }

}

// media stocks-page-5
@media (max-width: 800px) {
    .stocks-page {
        &__sort {
            margin-bottom: 30px;
        }
        &__card {
            padding: 0px;
            flex-direction: column-reverse;
            gap: 0px;
            &_main {
                gap: 20px;
                padding: 20px;
            }
            &_img img {
                max-height: 300px;
            }
        }
    }
}

@media (max-width: 688px) {
    .stocks-page {
        &__card {
            &_date {
                padding: 8px 10px;
            }
            &_name {
                font-size: 20px;
                margin-bottom: 12px;
                font-weight: 400;
            }
            &_btn {
                width: 100%;
            }
            &_img img {
                height: 200px;
            }
        }

        &__btn-arrow {
            display: block;
        }
        &__tab {
            &_btns {
                display: flex;
                flex-wrap: nowrap;
                flex-direction: column;
                gap: 0;
                max-height: 51px;
                overflow: hidden;
                margin-bottom: 0;
                transition: all 0.3s ease;
            }
            &_btns.acc-active {
                max-height: 400px;
                .stocks-page__btn-arrow {
                    transform: rotate(180deg);
                }
            }
            &_btn {
                border-radius: 0;
                padding: 16px 0;
                background: transparent;
                text-align: left;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin: 0 20px;
                .stocks-page__btn-arrow {
                    opacity: 0;
                }
                &.active {
                    margin: 0;
                    order: -1;
                    border-radius: 30px;
                    padding: 16px 20px;
                    border: 1px solid var(--liniya);
                    .stocks-page__btn-arrow {
                        opacity: 1;
                    }
                }
            }
        }
        &__btn-arrow {
            display: flex;
            margin: 0;
            &::after {
                transform: rotate(135deg);
                margin: 0;
                margin-bottom: 4px;
            }
        }
    }
}
