// about-2
.about {
    &__grid {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    &__item_between {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 20px;
    }

    &__title {
        margin-bottom: 20px;
    }

    &__text p:not(:last-child){
        margin-bottom: 20px;
    }

    &__img img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px;
        overflow: hidden;
    }

    &__advantages {
        display: flex;
        flex-direction: column;
        //gap: 10px;
    }

    &__advantage {
        flex-grow: 1;
        border-radius: 20px;
        padding: 25px;
        background: #414256;
        display: flex;
        flex-direction: column;
        gap: 10px;
        font-size: 14px;
        line-height: 130%;
        color: var(--seryy);

        & p:first-child {
            font-weight: 500;
            & span {
                font-weight: 700;
                font-size: 48px;
                line-height: 100%;
            }
        }

        &:nth-child(2) {
            background: #4b4c60;
            margin-top: -10px;
        }
        &:nth-child(3) {
            background: #55566a;
            margin-top: -10px;
        }
    }
}

.about-page {
    & .about__advantages {
        flex-direction: row;
        margin-bottom: 60px;
    }
    & .about__advantage {
        &:nth-child(2) {
            margin-top: 0;
            margin-left: -10px;
        }
        &:nth-child(3) {
            margin-top: 0;
            margin-left: -10px;
        }
    }
    & .about__item:first-child {
        grid-column: span 2;
    }
}

// media about-2
@media (max-width: 1050px) {
    .about {
        &__grid {
            grid-template-columns: repeat(1, 1fr);
            gap: 20px;
        }
        &__img img {
            max-height: 400px;
            object-position: top;
        }
    }
    .about-page {
        & .about__grid {
            grid-template-columns: repeat(3, 1fr);
        }
    }
}
@media (max-width: 860px) {
    .about-page {
        &__inner {
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        & .about__grid {
            grid-template-columns: repeat(1, 1fr);
        }
        & .about__advantages {
            order: 2;
            flex-direction: column;
            margin-bottom: 0;
        }
        & .about__advantage {
            &:nth-child(2) {
                margin-top: -10px;
                margin-left: 0;
            }
            &:nth-child(3) {
                margin-top: -10px;
                margin-left: 0;
            }
        }
        & .about__item:first-child {
            grid-column: span 1;
        }
    }
}
@media (max-width: 688px) {
    .about {
        &__text p:not(:last-child){
            margin-bottom: 14px;
        }
        &__img img {
            max-height: 200px;
        }
        &__advantage {
            border-radius: 20px;
            padding: 24px;
            gap: 6px;
            & p:first-child {
                font-size: 12px;
                & span {
                    font-size: 24px;
                }
            }
            &:nth-child(2), &:nth-child(3)  {
                margin-top: -5px;
            }
        }
    }
}